import { reactive } from 'vue'
import { supabase } from '@/services/supabase'

const state = reactive({
  notifications: [],
  show: false
})

const notificationsStore = {
  state,

  notify(message, type = 'info', timeout = 5000, id = Date.now()) {
    const notification = {
      id,
      message,
      type,
      timestamp: new Date(),
      read: false
    }

    const existingIndex = this.state.notifications.findIndex(n => n.id === id)
    if (existingIndex !== -1) {
      this.state.notifications[existingIndex] = notification
    } else {
      this.state.notifications.push(notification)
    }

    // Only auto-dismiss success notifications
    if (type === 'success' && timeout) {
      setTimeout(() => {
        this.remove(id)
      }, timeout)
    }

    return id
  },

  success(message, timeout) {
    return this.notify(message, 'success', timeout)
  },

  error(message, timeout) {
    return this.notify(message, 'error', timeout)
  },

  warning(message, timeout) {
    return this.notify(message, 'warning', timeout)
  },

  info(message, timeout) {
    return this.notify(message, 'info', timeout)
  },

  async markAsRead(id) {
    const notification = this.state.notifications.find(n => n.id === id)
    if (notification) {
      notification.read = true
      
      // Update in database if it's a persisted notification
      if (typeof id === 'string') {
        await supabase
          .from('notifications')
          .update({ read: true })
          .eq('id', id)
      }
    }
  },

  async markAllAsRead() {
    // Mark all local notifications as read
    this.state.notifications.forEach(n => n.read = true)

    // Update all unread notifications in database
    await supabase
      .from('notifications')
      .update({ read: true })
      .eq('read', false)
  },

  async loadUserNotifications() {
    const { data: { user } } = await supabase.auth.getUser()
    if (!user) return

    const { data, error } = await supabase
      .from('notifications')
      .select('*')
      .or(`recipient_id.eq.${user.id},recipient_id.is.null`)
      .order('created_at', { ascending: false })

    if (!error && data) {
      data.forEach(notification => {
        this.notify(
          notification.message,
          notification.type,
          null,
          notification.id
        )
      })
    }
  },

  remove(id) {
    const index = this.state.notifications.findIndex(n => n.id === id)
    if (index !== -1) {
      this.state.notifications.splice(index, 1)
    }
  },

  clear() {
    this.state.notifications = []
  },

  async sendToAll({ message, type = 'info', duration = 5000 }) {
    try {
      const { data: { user } } = await supabase.auth.getUser()
      
      const { error } = await supabase.from('notifications').insert({
        message,
        type,
        sender_id: user.id,
        recipient_id: null, // null means send to all
        duration
      })

      if (error) throw error
    } catch (error) {
      console.error('Error sending notification:', error)
      throw error
    }
  },

  async sendToUsers({ userIds, message, type = 'info', duration = 5000 }) {
    try {
      const { data: { user } } = await supabase.auth.getUser()
      
      const notifications = userIds.map(recipientId => ({
        message,
        type,
        sender_id: user.id,
        recipient_id: recipientId,
        duration
      }))

      const { error } = await supabase.from('notifications').insert(notifications)

      if (error) throw error
    } catch (error) {
      console.error('Error sending notifications:', error)
      throw error
    }
  },

  // Subscribe to realtime notifications
  subscribeToNotifications(userId) {
    return supabase
      .channel('notifications')
      .on('postgres_changes', {
        event: 'INSERT',
        schema: 'public',
        table: 'notifications',
        filter: `recipient_id=is.null OR recipient_id=eq.${userId}`
      }, (payload) => {
        this.notify(
          payload.new.message,
          payload.new.type,
          payload.new.duration
        )
      })
      .subscribe()
  }
}

export default notificationsStore
