<template>
  <div class="news-manager">
    <div class="panel-header">
      <h2>News Articles</h2>
      <button class="add-button" @click="showAddModal">
        <span class="material-symbols-rounded">post_add</span>
        Create Article
      </button>
    </div>

    <div class="articles-list">
      <div v-for="article in articles" :key="article.id" class="article-card">
        <div class="article-content">
          <span class="article-date">{{ formatDate(article.created_at) }}</span>
          <h3>{{ article.title }}</h3>
          <p>{{ article.excerpt }}</p>
          <div class="article-tags">
            <span v-for="tag in article.tags" :key="tag" class="tag">{{ tag }}</span>
          </div>
        </div>
        <div class="article-actions">
          <button @click="editArticle(article)" class="icon-button">
            <span class="material-symbols-rounded">edit</span>
          </button>
          <button @click="deleteArticle(article.id)" class="icon-button danger">
            <span class="material-symbols-rounded">delete</span>
          </button>
        </div>
      </div>
    </div>

    <!-- Article Modal -->
    <Modal v-if="showModal" @close="closeModal">
      <template #header>
        <h2>{{ editingArticle ? 'Edit Article' : 'Create Article' }}</h2>
      </template>
      <template #default>
        <form @submit.prevent="saveArticle" class="article-form">
          <div class="form-group">
            <label>Title *</label>
            <input v-model="articleForm.title" required type="text">
          </div>
          
          <div class="form-group">
            <label>Excerpt *</label>
            <input v-model="articleForm.excerpt" required type="text">
          </div>
          
          <div class="form-group">
            <label>Content *</label>
            <textarea v-model="articleForm.content" required rows="6"></textarea>
          </div>
          
          <div class="form-group">
            <label>Tags</label>
            <input 
              v-model="tagInput"
              @keydown.enter.prevent="addTag"
              placeholder="Press enter to add tag"
            >
            <div class="tags-preview">
              <span v-for="tag in articleForm.tags" 
                    :key="tag" 
                    class="tag"
                    @click="removeTag(tag)"
              >
                {{ tag }} ×
              </span>
            </div>
          </div>

          <div class="form-actions">
            <button type="button" @click="closeModal">Cancel</button>
            <button type="submit" class="primary">Save Article</button>
          </div>
        </form>
      </template>
    </Modal>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { supabase } from '@/services/supabase'
import Modal from '@/components/Modal.vue'

export default {
  name: 'NewsManager',
  components: { Modal },
  setup() {
    const articles = ref([])
    const showModal = ref(false)
    const editingArticle = ref(null)
    const tagInput = ref('')
    const articleForm = ref({
      title: '',
      excerpt: '',
      content: '',
      tags: []
    })

    const resetForm = () => {
      articleForm.value = {
        title: '',
        excerpt: '',
        content: '',
        tags: []
      }
      editingArticle.value = null
      tagInput.value = ''
    }

    const fetchArticles = async () => {
      const { data, error } = await supabase
        .from('news_articles')
        .select('*')
        .order('created_at', { ascending: false })

      if (!error) articles.value = data
    }

    const showAddModal = () => {
      resetForm()
      showModal.value = true
    }

    const editArticle = (article) => {
      editingArticle.value = article
      articleForm.value = { ...article }
      showModal.value = true
    }

    const saveArticle = async () => {
      const article = {
        ...articleForm.value,
        updated_at: new Date()
      }

      if (editingArticle.value) {
        await supabase
          .from('news_articles')
          .update(article)
          .eq('id', editingArticle.value.id)
      } else {
        await supabase
          .from('news_articles')
          .insert([article])
      }

      await fetchArticles()
      closeModal()
    }

    const deleteArticle = async (id) => {
      if (confirm('Are you sure you want to delete this article?')) {
        await supabase
          .from('news_articles')
          .delete()
          .eq('id', id)
        await fetchArticles()
      }
    }

    const addTag = () => {
      const tag = tagInput.value.trim()
      if (tag && !articleForm.value.tags.includes(tag)) {
        articleForm.value.tags.push(tag)
      }
      tagInput.value = ''
    }

    const removeTag = (tag) => {
      articleForm.value.tags = articleForm.value.tags.filter(t => t !== tag)
    }

    const closeModal = () => {
      showModal.value = false
      resetForm()
    }

    onMounted(fetchArticles)

    return {
      articles,
      showModal,
      editingArticle,
      articleForm,
      tagInput,
      showAddModal,
      editArticle,
      saveArticle,
      deleteArticle,
      addTag,
      removeTag,
      closeModal,
      formatDate: (date) => new Date(date).toLocaleDateString()
    }
  }
}
</script>

<style scoped>
.news-manager {
  background: var(--card-background);
  border-radius: 16px;
  border: 1px solid var(--card-border);
  overflow: hidden;
  padding: 24px;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.panel-header h2 {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.articles-list {
  display: grid;
  gap: 16px;
}

.article-card {
  background: var(--surface-background);
  border-radius: 12px;
  border: 1px solid var(--card-border);
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.article-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.article-content {
  flex: 1;
}

.article-date {
  font-size: 13px;
  color: var(--text-tertiary);
  display: block;
  margin-bottom: 8px;
}

.article-content h3 {
  margin: 0 0 8px 0;
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
}

.article-content p {
  margin: 0 0 12px 0;
  color: var(--text-secondary);
  font-size: 14px;
  line-height: 1.5;
}

.article-tags {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.tag {
  background: var(--card-background);
  color: var(--text-secondary);
  padding: 4px 10px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  border: 1px solid var(--card-border);
}

.tag:hover {
  background: var(--background-elevated);
  color: var(--text-primary);
}

.article-actions {
  display: flex;
  gap: 8px;
}

/* Form Styles */
.article-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-size: 13px;
  font-weight: 500;
  color: var(--text-secondary);
}

.form-group input,
.form-group textarea {
  padding: 12px;
  border-radius: 8px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 15px;
  font-family: inherit;
  transition: all 0.2s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 2px var(--accent-primary-alpha);
}

.tags-preview {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  margin-top: 8px;
  min-height: 32px;
  padding: 4px;
}

.tag {
  display: inline-flex;
  align-items: center;
  background: var(--surface-background);
  color: var(--text-secondary);
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 13px;
  border: 1px solid var(--card-border);
  cursor: pointer;
  transition: all 0.2s ease;
}

.tag:hover {
  background: var(--background-elevated);
  color: var(--system-red);
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 12px;
}

.form-actions button {
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
}

.form-actions button:not(.primary) {
  background: var(--surface-background);
  color: var(--text-primary);
}

.form-actions button.primary {
  background: var(--accent-primary);
  color: white;
}

.form-actions button:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

.add-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  background: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.add-button:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.icon-button {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 8px;
  background: var(--surface-background);
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s ease;
}

.icon-button:hover {
  background: var(--background-elevated);
  color: var(--text-primary);
}

.icon-button.danger:hover {
  background: var(--system-red);
  color: white;
}

/* Dark mode adjustments */
@media (prefers-color-scheme: dark) {
  .article-card {
    backdrop-filter: blur(20px);
  }
}
</style>
