<template>
  <div class="cart-overlay" v-if="show" @click="$emit('close')">
    <div class="cart-slideover" @click.stop>
      <div class="cart-header">
        <h2>Shopping Cart</h2>
        <button class="close-button" @click="$emit('close')">
          <span class="material-symbols-rounded">close</span>
        </button>
      </div>

      <div v-if="!cart.items.length" class="empty-cart">
        <span class="material-symbols-rounded">shopping_cart</span>
        <p>Your cart is empty</p>
      </div>

      <div v-else class="cart-items">
        <div v-for="item in cart.items" :key="item.id" class="cart-item">
          <img :src="item.image_url" :alt="item.name">
          <div class="item-details">
            <h3>{{ item.name }}</h3>
            <div class="item-price">${{ item.price }}</div>
            <div class="quantity-controls">
              <button @click="decrementQuantity(item)">-</button>
              <span>{{ item.quantity }}</span>
              <button @click="incrementQuantity(item)">+</button>
            </div>
          </div>
          <button class="remove-item" @click="removeItem(item)">
            <span class="material-symbols-rounded">delete</span>
          </button>
        </div>
      </div>

      <div class="cart-footer" v-if="cart.items.length">
        <div class="subtotal">
          <span>Subtotal</span>
          <strong>${{ cart.total.toFixed(2) }}</strong>
        </div>
        <button class="checkout-button" @click="checkout">
          <span class="material-symbols-rounded">payments</span>
          Checkout
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue'
import cart from '@/store/cart'
import { loadStripe } from '@stripe/stripe-js'
import { supabase } from '@/services/supabase'

export default defineComponent({
  name: 'CartSlideOver',
  props: {
    show: Boolean
  },
  setup() {
    async function checkout() {
      try {
        // Create Stripe checkout session
        const { data: session, error } = await supabase
          .functions.invoke('create-checkout-session', {
            body: { items: cart.items }
          })

        if (error) throw error

        // Load Stripe
        const stripe = await loadStripe(process.env.VUE_APP_STRIPE_PUBLIC_KEY)
        
        // Redirect to checkout
        await stripe.redirectToCheckout({
          sessionId: session.id
        })
      } catch (error) {
        console.error('Checkout error:', error)
        alert('Failed to initiate checkout')
      }
    }

    function incrementQuantity(item) {
      cart.updateQuantity(item.id, item.quantity + 1)
    }

    function decrementQuantity(item) {
      if (item.quantity > 1) {
        cart.updateQuantity(item.id, item.quantity - 1)
      }
    }

    function removeItem(item) {
      cart.removeItem(item.id)
    }

    return {
      cart,
      checkout,
      incrementQuantity,
      decrementQuantity,
      removeItem
    }
  }
})
</script>

<style scoped>
.cart-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  z-index: 100;
}

.cart-slideover {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  max-width: 480px;
  height: 100%;
  background: var(--background-primary);
  box-shadow: var(--shadow-lg);
  display: flex;
  flex-direction: column;
  animation: slideIn 0.3s ease-out;
}

.cart-header {
  padding: 24px;
  border-bottom: 1px solid var(--card-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-items {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
}

.cart-item {
  display: flex;
  gap: 16px;
  padding: 16px;
  background: var(--surface-background);
  border-radius: 12px;
  margin-bottom: 16px;
}

.cart-item img {
  width: 80px;
  height: 80px;
  object-fit: contain;
  border-radius: 8px;
}

.item-details {
  flex: 1;
}

.item-details h3 {
  margin: 0 0 8px 0;
  font-size: 16px;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-top: 8px;
}

.quantity-controls button {
  width: 24px;
  height: 24px;
  border-radius: 6px;
  border: none;
  background: var(--card-background);
  color: var(--text-primary);
  cursor: pointer;
}

.cart-footer {
  padding: 24px;
  border-top: 1px solid var(--card-border);
}

.subtotal {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.checkout-button {
  width: 100%;
  padding: 16px;
  border: none;
  border-radius: 12px;
  background: var(--accent-primary);
  color: white;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

@keyframes slideIn {
  from { transform: translateX(100%); }
  to { transform: translateX(0); }
}
</style>
