<template>
  <div class="set-filter">
    <div class="set-list" :class="{ expanded: isExpanded }">
      <div class="set-grid" ref="setGrid">
        <!-- All Sets button as first item in grid -->
        <button 
          class="set-button all-sets"
          :class="{ active: !selectedSet }"
          @click="$emit('clear')"
        >
          <div class="set-content">
            <div class="set-icon">
              <span class="material-symbols-rounded">category</span>
            </div>
            <div class="set-info">
              <span class="set-name">All Sets</span>
              <span class="set-date">{{ sets.length }} sets available</span>
            </div>
          </div>
        </button>

        <!-- Regular set buttons -->
        <button 
          v-for="set in displayedSets" 
          :key="set.id"
          class="set-button"
          :class="{ active: selectedSet === set.id }"
          @click="selectSet(set.id)"
        >
          <div class="set-content">
            <div class="set-icon">
              <img 
                v-if="hasLogo(set)" 
                :src="set.images.logo" 
                :alt="set.name"
                class="set-logo"
                loading="lazy"
              />
              <span v-else class="material-symbols-rounded">style</span>
            </div>
            <div class="set-info">
              <span class="set-name">{{ set.name }}</span>
              <span class="set-date">{{ formatDate(set.releaseDate) }}</span>
            </div>
            <div class="selection-highlight"></div>
          </div>
        </button>
      </div>
    </div>
    
    <button 
      v-if="canLoadMore"
      class="load-more-button"
      @click="loadMoreSets"
    >
      <span class="material-symbols-rounded">add_circle</span>
      Load More Sets ({{ remainingSets }} remaining)
    </button>
  </div>
</template>

<script>
export default {
  name: 'SetFilter',
  props: {
    sets: {
      type: Array,
      default: () => []
    },
    selectedSet: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      displayCount: 4, // Initial number of sets to show
      increment: 10, // How many more to load each time
      isExpanded: false,
      defaultVisibleCount: 10,
      canScrollUp: false,
      canScrollDown: false
    }
  },
  computed: {
    displayedSets() {
      return this.sets.slice(0, this.displayCount)
    },
    canLoadMore() {
      return this.displayCount < this.sets.length
    },
    remainingSets() {
      return this.sets.length - this.displayCount
    },
    visibleSets() {
      return this.isExpanded ? this.sets : this.sets.slice(0, this.defaultVisibleCount)
    }
  },
  methods: {
    hasLogo(set) {
      return set.images && set.images.logo
    },
    toggleExpand() {
      this.isExpanded = !this.isExpanded
    },
    formatDate(dateStr) {
      if (!dateStr) return '';
      return new Date(dateStr).toLocaleDateString('en-US', { 
        year: 'numeric',
        month: 'short'
      });
    },
    selectSet(setId) {
      this.$emit('select', setId);
      this.$nextTick(() => {
        const selectedButton = this.$refs.setGrid.querySelector('.set-button.active');
        if (selectedButton) {
          selectedButton.scrollIntoView({ behavior: 'smooth', block: 'nearest' });
        }
      });
    },
    checkScroll() {
      const grid = this.$refs.setGrid;
      if (!grid) return;
      
      this.canScrollUp = grid.scrollTop > 0;
      this.canScrollDown = grid.scrollTop < (grid.scrollHeight - grid.clientHeight);
    },
    loadMoreSets() {
      this.displayCount = Math.min(
        this.displayCount + this.increment,
        this.sets.length
      )
    }
  },
  mounted() {
    const grid = this.$refs.setGrid;
    if (grid) {
      grid.addEventListener('scroll', this.checkScroll);
      this.checkScroll();
    }
  },
  beforeUnmount() {
    const grid = this.$refs.setGrid;
    if (grid) {
      grid.removeEventListener('scroll', this.checkScroll);
    }
  }
}
</script>

<style scoped>
.set-filter {
  position: relative;
  margin-bottom: 24px;
  background: var(--card-background);
  border-radius: 16px;
  padding: 16px;
  border: 1px solid var(--card-border);
}

.set-list {
  position: relative;
  background: var(--surface-background);
  border-radius: 12px;
  padding: 16px;
  transition: all 0.3s ease;
}

.set-list.expanded {
  max-height: 480px; /* Increased height for better scrolling */
}

.set-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 12px;
  overflow: visible;
  max-height: none;
}

.set-grid::-webkit-scrollbar {
  width: 6px;
}

.set-grid::-webkit-scrollbar-track {
  background: transparent;
}

.set-grid::-webkit-scrollbar-thumb {
  background: var(--accent-primary);
  border-radius: 3px;
}

/* Updated expand button styling */
.expand-button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin: 16px auto 0;
  padding: 10px 24px;
  background: var(--accent-secondary);
  border: none;
  border-radius: 100px;
  color: var(--accent-primary);
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.expand-button:hover {
  background: var(--accent-primary);
  color: white;
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

.expand-button .material-symbols-rounded {
  font-size: 20px;
  transition: transform 0.3s ease;
}

.expanded .expand-button .material-symbols-rounded {
  transform: rotate(180deg);
}

/* Add subtle animation when expanding/collapsing */
.set-list::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 40px;
  background: linear-gradient(to top, var(--surface-background) 0%, transparent 100%);
  pointer-events: none;
  opacity: 0.8;
  transition: opacity 0.3s ease;
}

.set-list.expanded::after {
  opacity: 0;
}

.set-button {
  position: relative;
  overflow: hidden;
  border: 1px solid var(--card-border);
  border-radius: 12px;
  background: var(--surface-background);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
}

.set-button:hover {
  border-color: var(--accent-primary-alpha);
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

.set-button:hover .set-icon {
  background: var(--accent-secondary);
}

.set-button:hover .set-logo {
  transform: scale(1.1);
}

.set-button.active {
  border-color: var(--accent-primary);
  background: var(--accent-primary-alpha);
}

.set-button.active .set-icon {
  background: var(--accent-primary);
}

.set-button.active .set-name {
  color: var(--accent-primary);
  font-weight: 600;
}

.set-content {
  position: relative;
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 12px 16px;
  z-index: 1;
}

.set-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background: var(--background-elevated);
  flex-shrink: 0;
  transition: all 0.3s ease;
}

.set-logo {
  width: 28px;
  height: 28px;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.set-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.set-name {
  font-weight: 500;
  color: var(--text-primary);
  transition: color 0.3s ease;
}

.set-date {
  font-size: 12px;
  color: var(--text-secondary);
}

.selection-highlight {
  position: absolute;
  inset: 0;
  background: var(--accent-primary);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.scroll-shadow {
  position: absolute;
  left: 0;
  right: 0;
  height: 40px;
  pointer-events: none;
  z-index: 2;
}

.scroll-shadow.top {
  top: 0;
  background: linear-gradient(to bottom, 
    var(--card-background) 0%,
    rgba(var(--card-background-rgb), 0) 100%
  );
}

.scroll-shadow.bottom {
  bottom: 0;
  background: linear-gradient(to top, 
    var(--card-background) 0%,
    rgba(var(--card-background-rgb), 0) 100%
  );
}

@keyframes selectScale {
  0% { transform: scale(1); }
  50% { transform: scale(1.02); }
  100% { transform: scale(1); }
}

.scale-animation {
  animation: selectScale 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

@media (max-width: 768px) {
  .set-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }

  .set-content {
    padding: 10px 12px;
  }

  .set-icon {
    width: 32px;
    height: 32px;
  }

  .set-logo {
    width: 24px;
    height: 24px;
  }
}

.set-button.reset {
  background: var(--background-elevated);
  font-weight: 600;
  border: 1px solid var(--card-border);
  color: var(--text-primary);
}

.set-button.reset:hover {
  background: var(--accent-secondary);
  color: var(--accent-primary);
  border-color: var(--accent-primary);
}

.set-button.reset.active {
  background: var(--accent-primary);
  color: white;
  border-color: transparent;
}

.selection-indicator {
  position: absolute;
  inset: 0;
  background: var(--accent-primary);
  transform: translateX(-100%);
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.set-button.active .selection-indicator {
  transform: translateX(0);
}

@keyframes fadeIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Stagger animation for set buttons */
.set-button:nth-child(1) { animation-delay: 0.05s; }
.set-button:nth-child(2) { animation-delay: 0.1s; }
.set-button:nth-child(3) { animation-delay: 0.15s; }
.set-button:nth-child(4) { animation-delay: 0.2s; }
.set-button:nth-child(5) { animation-delay: 0.25s; }

.all-sets {
  background: var(--background-elevated);
  border: 1px solid var(--accent-primary-alpha);
}

.all-sets:hover {
  background: var(--accent-secondary);
}

.all-sets.active {
  background: var(--accent-primary);
  border-color: transparent;
}

.all-sets.active .set-name,
.all-sets.active .set-date {
  color: rgb(255, 255, 255);
}

.load-more-button {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin-top: 16px;
  padding: 16px;
  background: var(--background-elevated);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.load-more-button:hover {
  background: var(--accent-primary);
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.load-more-button .material-symbols-rounded {
  font-size: 20px;
  transition: transform 0.3s ease;
}

.load-more-button:hover .material-symbols-rounded {
  transform: rotate(180deg);
}
</style>
