<template>
  <div class="card" @click="$emit('card-selected', card)">
    <div class="card-image-container">
      <img 
        :src="imageUrl" 
        :alt="card.name"
        class="card-image"
        @error="handleImageError"
        ref="cardImage"
      />
      <div v-if="!hasValidImage" class="image-placeholder">
        <span class="material-symbols-rounded">broken_image</span>
        <span>No image available</span>
      </div>
    </div>
    <div class="card-details">
      <h3 class="card-name">{{ card.name }}</h3>
      <p class="card-type">{{ card.type }}</p>
      <p class="card-rarity">{{ card.rarity }}</p>
      <p class="card-price">{{ formatPrice(card.price) }}</p>
    </div>
  </div>
</template>

<script>
import { getProxiedImageUrl, handleImageError } from '@/utils/imageProxy'

export default {
  name: 'Card',
  props: {
    card: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      hasValidImage: true
    }
  },
  setup(props) {
    const imageUrl = computed(() => getProxiedImageUrl(props.card.imageUrl))

    return {
      imageUrl,
      handleImageError
    }
  },
  methods: {
    handleClick() {
      this.$emit('card-selected', this.card);
    },
    formatPrice(price) {
      return price === 'N/A' ? 'N/A' : `$${Number(price).toFixed(2)}`;
    }
  }
}
</script>

<style scoped>
.card {
  background: var(--card-background);
  backdrop-filter: var(--blur-sm);
  -webkit-backdrop-filter: var(--blur-sm);
  border-radius: 16px;
  box-shadow: var(--shadow-lg);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
  border: 1px solid var(--card-border);
  height: 100%;
  display: flex;
  flex-direction: column;
  transform-origin: center;
  will-change: transform;
  max-width: 100%; /* Ensure card doesn't overflow grid cell */
}

.card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: var(--shadow-lg), 0 0 0 1px var(--accent-primary);
}

.card-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s cubic-bezier(0.2, 1, 0.3, 1);
}

.card:hover .card-image {
  transform: scale(1.05);
}

.card-details {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 12px; /* Slightly reduced padding */
  background: linear-gradient(
    to bottom,
    var(--card-background),
    var(--background-secondary)
  );
}

.card-name {
  font-size: 15px; /* Slightly smaller font size */
  font-weight: 600;
  letter-spacing: -0.01em;
  margin: 0 0 8px 0;
  color: var(--text-primary);
}

.card-type,
.card-rarity {
  font-size: 13px;
  color: var(--text-secondary);
  margin: 0;
  letter-spacing: -0.01em;
}

.card-price {
  margin-top: 12px;
  font-size: 15px;
  font-weight: 600;
  color: var(--accent-primary);
  letter-spacing: -0.01em;
}

.card-image-container {
  flex-shrink: 0;
  height: 0;
  padding-bottom: 135%; /* Slightly adjusted aspect ratio */
  position: relative;
  overflow: hidden;
  background: var(--background-elevated);
  border-radius: 16px 16px 0 0;
}

.image-placeholder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: var(--background-elevated);
  color: var(--text-secondary);
  gap: 8px;
}

.image-placeholder .material-symbols-rounded {
  font-size: 48px;
  opacity: 0.5;
}
</style>