<template>
  <div class="home">
    <header class="page-header">
      <div class="header-content">
        <h1>Welcome back!</h1>
        <p class="subtitle">Track your Pokémon card collection and market trends</p>
      </div>
    </header>

    <div class="widgets-grid">
      <MarketWidget class="widget" />
      <TrendingCardsWidget class="widget" />
    </div>

    <div class="widget-grid">
      <div class="widget highlight-widget">
        <span class="material-symbols-rounded widget-icon">diamond</span>
        <h2>Most Expensive Card</h2>
        <div class="card-preview" v-if="mostExpensiveCard">
          <img :src="mostExpensiveCard.imageUrl" :alt="mostExpensiveCard.name">
          <div class="card-info">
            <h3>{{ mostExpensiveCard.name }}</h3>
            <p class="price">${{ mostExpensiveCard.price }}</p>
          </div>
        </div>
      </div>

      <div class="widget">
        <span class="material-symbols-rounded widget-icon">trending_up</span>
        <h2>Market Trends</h2>
        <ul class="trend-list">
          <li v-for="trend in trends" :key="trend.id">
            <span class="trend-name">{{ trend.name }}</span>
            <span class="trend-change" :class="trend.direction">
              {{ trend.change }}%
            </span>
          </li>
        </ul>
      </div>

      <div class="widget">
        <span class="material-symbols-rounded widget-icon">newspaper</span>
        <h2>Latest News</h2>
        <div class="news-list">
          <article v-for="item in news" :key="item.id" class="news-item">
            <h4>{{ item.title }}</h4>
            <p>{{ item.excerpt }}</p>
            <span class="news-date">{{ item.date }}</span>
          </article>
        </div>
      </div>

      <div class="widget">
        <span class="material-symbols-rounded widget-icon">star</span>
        <h2>Top Valued Cards</h2>
        <ol class="value-list">
          <li v-for="card in topValuedCards" :key="card.id">
            {{ card.name }} - ${{ card.price }}
          </li>
        </ol>
      </div>
    </div>

    <div v-if="!isLoggedIn" class="cta-section">
      <div class="cta-content">
        <h2>Want to track your collection?</h2>
        <p>Create a free account to manage your Pokémon card collection and wishlist.</p>
        <div class="cta-buttons">
          <router-link to="/register" class="cta-button primary">
            Create Account
          </router-link>
          <router-link to="/login" class="cta-button secondary">
            Sign In
          </router-link>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import Collection from '@/components/Collection.vue'
import MarketWidget from '@/components/widgets/MarketWidget.vue'
import TrendingCardsWidget from '@/components/widgets/TrendingCardsWidget.vue'
import pokemonApi from '@/services/pokemonApi';
import auth from '@/store/auth'

export default {
  name: 'Home',
  components: {
    Collection,
    MarketWidget,
    TrendingCardsWidget
  },
  data() {
    return {
      mostExpensiveCard: null,
      topValuedCards: [],
      trends: [
        { id: 1, name: 'Charizard Base Set', change: '+12.5', direction: 'up' },
        { id: 2, name: 'Pikachu Illustrator', change: '+5.2', direction: 'up' },
        { id: 3, name: 'Mewtwo GX', change: '-2.1', direction: 'down' }
      ],
      news: [
        {
          id: 1,
          title: 'New Scarlet & Violet Set Announced',
          excerpt: 'The Pokémon Company reveals exciting new cards...',
          date: '2 hours ago'
        },
        {
          id: 2,
          title: 'Record Breaking Card Sale',
          excerpt: 'Rare Pikachu card sells for unprecedented amount...',
          date: '1 day ago'
        }
      ]
    };
  },
  async mounted() {
    try {
      const response = await pokemonApi.getCards(1, 100);
      const cards = response.data.map(card => ({
        id: card.id,
        name: card.name,
        imageUrl: card.images.small,
        price: card.cardmarket && card.cardmarket.prices ? card.cardmarket.prices.averageSellPrice : 0
      })).filter(card => card.price > 0);

      // Sort by price
      cards.sort((a, b) => b.price - a.price);
      
      this.mostExpensiveCard = cards[0];
      this.topValuedCards = cards.slice(0, 5);
    } catch (error) {
      console.error('Error fetching cards:', error);
    }
  },
  computed: {
    isLoggedIn() {
      return !!auth.state.session
    }
  }
};
</script>

<style scoped>
.home {
  padding: 24px;
  max-width: 1440px;
  margin: 0 auto;
}

.page-header {
  margin-bottom: 32px;
  padding: 40px;
  background: linear-gradient(135deg, var(--accent-primary), var(--system-purple));
  border-radius: 24px;
  color: white;
}

.header-content {
  max-width: 600px;
}

.header-content h1 {
  font-size: 36px;
  margin: 0;
  font-weight: 700;
}

.subtitle {
  font-size: 18px;
  opacity: 0.9;
  margin: 8px 0 0 0;
}

.widgets-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 24px;
  margin-bottom: 32px;
}

.widget-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  margin: 20px 0;
}

.widget {
  background: var(--card-background);
  backdrop-filter: var(--blur-sm);
  -webkit-backdrop-filter: var(--blur-sm);
  border-radius: 20px;
  padding: 24px;
  border: 1px solid var(--card-border);
  box-shadow: var(--shadow-md);
}

.widget:hover {
  transform: translateY(-4px);
}

.widget h2 {
  font-size: 20px;
  font-weight: 600;
  letter-spacing: -0.02em;
  margin: 0 0 16px 0;
}

.highlight-widget {
  grid-column: span 2;
  background: linear-gradient(135deg, 
    var(--system-blue),
    var(--system-purple)
  );
  color: white;
}

.widget-icon {
  font-size: 32px;
  color: var(--accent-primary);
  margin-bottom: 16px;
}

.highlight-widget .widget-icon {
  color: white;
}

.card-preview {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-top: 16px;
}

.card-preview img {
  width: 140px;
  border-radius: 12px;
  box-shadow: var(--shadow-lg);
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.card-preview:hover img {
  transform: scale(1.05);
}

.card-info h3 {
  margin: 0 0 8px 0;
}

.price {
  font-size: 24px;
  font-weight: 600;
  color: var(--accent-secondary);
}

.trend-list, .value-list {
  list-style: none;
  padding: 0;
}

.trend-list li, .value-list li {
  display: flex;
  justify-content: space-between;
  padding: 12px 0;
  font-size: 15px;
  letter-spacing: -0.01em;
  border-bottom: 1px solid var(--divider-color);
}

.trend-change {
  font-weight: 500;
}

.trend-change.up { color: var(--system-green); }
.trend-change.down { color: var(--system-red); }

.news-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.news-item {
  padding-bottom: 16px;
  border-bottom: 1px solid var(--divider-color);
}

.news-item h4 {
  font-size: 16px;
  font-weight: 600;
  letter-spacing: -0.01em;
  margin: 0 0 8px 0;
  color: var(--text-primary);
}

.news-item p {
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: -0.01em;
  margin: 0;
  color: var(--text-secondary);
}

.news-date {
  display: block;
  margin-top: 8px;
  font-size: 12px;
  color: var(--text-tertiary);
}

.content-grid {
  display: grid;
  gap: 24px;
}

.cta-section {
  margin-top: 48px;
  padding: 48px;
  background: linear-gradient(135deg, var(--accent-primary), var(--system-purple));
  border-radius: 24px;
  text-align: center;
  color: white;
}

.cta-buttons {
  display: flex;
  gap: 16px;
  justify-content: center;
  margin-top: 24px;
}

.cta-button {
  padding: 12px 24px;
  border-radius: 8px;
  font-weight: 600;
  text-decoration: none;
}

.cta-button.primary {
  background: white;
  color: var(--accent-primary);
}

.cta-button.secondary {
  background: rgba(255, 255, 255, 0.2);
  color: white;
}

@media (max-width: 768px) {
  .home {
    padding: 16px;
  }

  .page-header {
    padding: 24px;
  }

  .header-content h1 {
    font-size: 28px;
  }

  .subtitle {
    font-size: 16px;
  }

  .widgets-grid {
    grid-template-columns: 1fr;
  }

  .highlight-widget {
    grid-column: span 1;
  }
  
  .card-preview {
    flex-direction: column;
    text-align: center;
  }
}
</style>