<template>
  <div class="product-form-container">
    <!-- Product Creation Mode Selection -->
    <div v-if="!selectedMode && !product" class="mode-selection">
      <button class="mode-button" @click="selectedMode = 'manual'">
        <span class="material-symbols-rounded">edit_note</span>
        <h3>Create Product from Scratch</h3>
        <p>Manually enter all product details</p>
      </button>
      
      <button class="mode-button" @click="selectedMode = 'search'">
        <span class="material-symbols-rounded">search</span>
        <h3>Search and Add Product</h3>
        <p>Search Pokemon TCG database to auto-fill details</p>
      </button>
    </div>

    <!-- Search Mode -->
    <div v-else-if="selectedMode === 'search'" class="search-mode">
      <button class="back-button" @click="selectedMode = null">
        <span class="material-symbols-rounded">arrow_back</span>
        Back to selection
      </button>

      <div class="search-container">
        <div class="search-box">
          <span class="material-symbols-rounded">search</span>
          <input 
            v-model="searchQuery"
            type="text"
            placeholder="Search by name, set, or number..."
            @input="debouncedSearch"
            ref="searchInput"
            :disabled="isSearching"
          >
          <div v-if="searchQuery" class="search-clear" @click="clearSearch">
            <span class="material-symbols-rounded">close</span>
          </div>
        </div>
      </div>

      <!-- Loading State -->
      <div v-if="isSearching" class="search-loading">
        <div class="loading-spinner"></div>
        <p>Searching cards...</p>
      </div>

      <!-- Search Error State -->
      <div v-if="searchError" class="search-error">
        <span class="material-symbols-rounded">error</span>
        <h3>Error searching cards</h3>
        <p>{{ searchError }}</p>
        <button @click="retrySearch" class="retry-button">
          Try Again
        </button>
      </div>

      <!-- No Results State with Suggestions -->
      <div v-else-if="searchQuery && !isSearching && !searchResults.length" class="no-results">
        <span class="material-symbols-rounded">search_off</span>
        <h3>No cards found</h3>
        <p>Try:</p>
        <ul class="search-tips">
          <li>Using the full card name (e.g., "Pikachu V")</li>
          <li>Checking for typos</li>
          <li>Using fewer keywords</li>
          <li>Searching by set name</li>
        </ul>
      </div>

      <!-- Search Results -->
      <div v-else-if="searchResults.length" class="search-results">
        <TransitionGroup name="card-result">
          <div 
            v-for="card in searchResults" 
            :key="card.id"
            class="card-result"
            @click="selectCard(card)"
          >
            <div class="card-image">
              <img 
                :src="card.images.small" 
                :alt="card.name"
                loading="lazy"
              >
              <div class="quick-actions">
                <button class="select-button">
                  <span class="material-symbols-rounded">add</span>
                  Select
                </button>
              </div>
            </div>
            <div class="card-info">
              <h4>{{ card.name }}</h4>
              <div class="card-meta">
                <span class="set-name">{{ card.set.name }}</span>
                <span 
                  class="rarity"
                  :class="getRarityClass(card.rarity)"
                >
                  {{ card.rarity }}
                </span>
              </div>
              <div class="price-info" v-if="getCardPrice(card)">
                <span class="material-symbols-rounded">payments</span>
                ${{ getCardPrice(card) }}
              </div>
            </div>
          </div>
        </TransitionGroup>

        <!-- Load More -->
        <div v-if="hasMoreResults" class="load-more">
          <button 
            @click="loadMoreResults"
            :disabled="isLoadingMore"
            class="load-more-button"
          >
            <span v-if="isLoadingMore" class="loading-spinner small"></span>
            <span v-else>Load More Results</span>
          </button>
        </div>
      </div>
    </div>

    <!-- Manual Form -->
    <form 
      v-else 
      @submit.prevent="handleSubmit" 
      class="product-form"
    >
      <button 
        v-if="!product && selectedMode === 'manual'"
        class="back-button" 
        @click="selectedMode = null"
      >
        <span class="material-symbols-rounded">arrow_back</span>
        Back to selection
      </button>

      <div class="form-section">
        <div class="form-group">
          <label>Name *</label>
          <input 
            v-model="form.name"
            type="text"
            required
            placeholder="Enter product name"
          >
        </div>

        <div class="form-group">
          <label>Type *</label>
          <select v-model="form.type" required>
            <option value="single">Single Card</option>
            <option value="booster">Booster Pack</option>
            <option value="set">Card Set</option>
          </select>
        </div>

        <div class="form-group">
          <label>Price ($) *</label>
          <input 
            v-model.number="form.price"
            type="number"
            step="0.01"
            min="0"
            required
          >
        </div>

        <div class="form-group">
          <label>Stock *</label>
          <input 
            v-model.number="form.stock"
            type="number"
            min="0"
            required
          >
        </div>

        <div class="form-group">
          <label>Image URL</label>
          <input 
            v-model="form.image_url"
            type="url"
            placeholder="https://..."
          >
        </div>

        <div class="form-group">
          <label>Description</label>
          <textarea 
            v-model="form.description"
            rows="3"
            placeholder="Enter product description"
          ></textarea>
        </div>

        <div class="form-group">
          <label class="checkbox-label">
            <input 
              type="checkbox"
              v-model="form.active"
            >
            Active (available for purchase)
          </label>
        </div>
      </div>

      <div class="form-actions">
        <button type="button" class="secondary" @click="$emit('cancel')">
          Cancel
        </button>
        <button type="submit" class="primary">
          {{ product ? 'Update Product' : 'Create Product' }}
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { pokemonTcgApi } from '@/services/pokemonTcgApi'
import debounce from 'lodash/debounce'

export default {
  name: 'ProductForm',
  props: {
    product: {
      type: Object,
      default: null
    }
  },
  emits: ['save', 'cancel'],
  setup(props, { emit }) {
    const selectedMode = ref(null)
    const searchQuery = ref('')
    const searchResults = ref([])
    const isSearching = ref(false)
    const isSubmitting = ref(false)
    const form = ref({
      name: '',
      type: 'single',
      price: 0,
      stock: 0,
      image_url: '',
      description: '',
      active: true,
      metadata: {},
      ...(props.product || {})
    })

    const currentPage = ref(1)
    const hasMoreResults = ref(false)
    const isLoadingMore = ref(false)
    const searchInput = ref(null)
    const searchError = ref(null)

    const debouncedSearch = debounce(async () => {
      currentPage.value = 1
      await performSearch()
    }, 500) // Increased debounce time

    async function performSearch() {
      if (!searchQuery.value || searchQuery.value.length < 2) {
        searchResults.value = [];
        return;
      }
      
      isSearching.value = true
      searchError.value = null
      
      try {
        const response = await pokemonTcgApi.searchCards({
          q: searchQuery.value,
          page: currentPage.value,
          pageSize: 20
        });
        
        if (currentPage.value === 1) {
          searchResults.value = response.data;
        } else {
          searchResults.value = [...searchResults.value, ...response.data];
        }
        
        hasMoreResults.value = response.hasMore;
        
        if (searchResults.value.length === 0) {
          searchError.value = 'No cards found matching your search.';
        }
      } catch (error) {
        console.error('Search error:', error);
        searchError.value = error.message;
        searchResults.value = [];
      } finally {
        isSearching.value = false;
      }
    }

    async function loadMoreResults() {
      isLoadingMore.value = true
      currentPage.value++
      await performSearch()
      isLoadingMore.value = false
    }

    function clearSearch() {
      searchQuery.value = ''
      searchResults.value = []
      currentPage.value = 1
      hasMoreResults.value = false
      if (searchInput.value) {
        searchInput.value.focus()
      }
    }

    function retrySearch() {
      searchError.value = null
      performSearch()
    }

    function getCardPrice(card) {
      if (card.cardmarket && 
          card.cardmarket.prices && 
          card.cardmarket.prices.averageSellPrice) {
        return card.cardmarket.prices.averageSellPrice.toFixed(2)
      }
      return null
    }

    function getRarityClass(rarity) {
      return rarity ? rarity.toLowerCase() : ''
    }

    function selectCard(card) {
      const cardPrice = getCardPrice(card) || 0
      console.log('Selected card:', card) // Add debug log
      console.log('Card price:', cardPrice) // Add debug log
      
      form.value = {
        name: card.name,
        type: 'single',
        price: parseFloat(cardPrice),
        stock: 0, // Always start with 0 stock
        image_url: card.images.large,
        description: card.flavorText || '',
        active: true,
        tcg_id: card.id,
        metadata: {
          set: card.set,
          rarity: card.rarity,
          number: card.number,
          artist: card.artist
        }
      }
      console.log('Updated form:', form.value) // Add debug log
      selectedMode.value = 'manual'
    }

    async function handleSubmit() {
      if (isSubmitting.value) return
      
      isSubmitting.value = true
      try {
        const productData = {
          ...form.value,
          price: parseFloat(form.value.price) || 0,
          stock: parseInt(form.value.stock) || 0
        }

        console.log('Submitting product data:', productData) // Add debug log
        await emit('save', productData)
        
        // Only reset if creating a new product
        if (!props.product) {
          form.value = {
            name: '',
            type: 'single',
            price: 0,
            stock: 0,
            image_url: '',
            description: '',
            active: true,
            metadata: {}
          }
          selectedMode.value = null
        }
      } catch (error) {
        console.error('Error submitting form:', error)
        alert('Failed to save product: ' + error.message)
      } finally {
        isSubmitting.value = false
      }
    }

    onMounted(() => {
      if (selectedMode.value === 'search' && searchInput.value) {
        searchInput.value.focus()
      }
    })

    return {
      selectedMode,
      searchQuery,
      searchResults,
      isSearching,
      form,
      isSubmitting,
      handleSubmit,
      getCardPrice,
      hasMoreResults,
      isLoadingMore,
      searchInput,
      debouncedSearch,
      loadMoreResults,
      clearSearch,
      getRarityClass,
      searchError,
      retrySearch,
      selectCard
    }
  }
}
</script>

<style scoped>
.product-form {
  padding: 24px 0;
}

.form-section {
  background: var(--surface-background);
  border-radius: 12px;
  border: 1px solid var(--card-border);
  overflow: hidden;
}

.form-group {
  padding: 16px;
  border-bottom: 1px solid var(--card-border);
}

.form-group:last-child {
  border-bottom: none;
}

.form-group label {
  display: block;
  font-size: 13px;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
  border: 1px solid var(--card-border);
  background: var(--card-background);
  color: var(--text-primary);
  font-size: 15px;
  transition: all 0.2s ease;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 2px var(--accent-primary-alpha);
}

.form-group textarea {
  resize: vertical;
  min-height: 100px;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.checkbox-label input[type="checkbox"] {
  width: auto;
  margin: 0;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 24px;
}

button {
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

button.secondary {
  border: none;
  background: var(--surface-background);
  color: var(--text-primary);
}

button.primary {
  border: none;
  background: var(--accent-primary);
  color: white;
}

button.primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* Mode Selection */
.mode-selection {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
  padding: 24px 0;
}

.mode-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  padding: 32px;
  border: 1px solid var(--card-border);
  border-radius: 16px;
  background: var(--surface-background);
  cursor: pointer;
  transition: all 0.2s ease;
}

.mode-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  border-color: var(--accent-primary);
}

.mode-button .material-symbols-rounded {
  font-size: 32px;
  color: var(--accent-primary);
}

.mode-button h3 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
}

.mode-button p {
  margin: 0;
  font-size: 14px;
  color: var(--text-secondary);
  text-align: center;
}

/* Back Button */
.back-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border: none;
  background: none;
  color: var(--text-secondary);
  font-size: 14px;
  cursor: pointer;
  margin-bottom: 24px;
}

/* Search Mode */
.search-container {
  display: flex;
  gap: 12px;
  margin-bottom: 24px;
}

.search-box {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  background: var(--surface-background);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  position: relative;
  transition: all 0.2s ease;
}

.search-box input {
  border: none;
  background: none;
  outline: none;
  width: 100%;
  font-size: 14px;
  color: var (--text-primary);
}

.search-clear {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  padding: 4px;
  cursor: pointer;
  color: var(--text-secondary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.search-clear:hover {
  background: var(--background-elevated);
  color: var(--text-primary);
}

.search-button {
  padding: 12px 24px;
  border: none;
  background: var(--accent-primary);
  color: white;
  border-radius: 12px;
  font-weight: 500;
  cursor: pointer;
}

.search-loading {
  text-align: center;
  padding: 48px 0;
  color: var (--text-secondary);
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid var(--card-border);
  border-top-color: var(--accent-primary);
  border-radius: 50%;
  margin: 0 auto 16px;
  animation: spin 1s linear infinite;
}

.loading-spinner.small {
  width: 20px;
  height: 20px;
  border-width: 2px;
  margin: 0;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.search-error,
.no-results {
  text-align: center;
  padding: 48px 24px;
  background: var(--surface-background);
  border-radius: 16px;
  border: 1px solid var(--card-border);
}

.no-results .material-symbols-rounded {
  font-size: 48px;
  margin-bottom: 16px;
}

.search-tips {
  list-style: none;
  padding: 0;
  margin: 16px 0 0;
  text-align: left;
  display: inline-block;
}

.search-tips li {
  margin: 8px 0;
  color: var(--text-secondary);
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.search-tips li::before {
  content: "•";
  color: var(--accent-primary);
  font-weight: bold;
}

.retry-button {
  margin-top: 16px;
  padding: 8px 16px;
  background: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.retry-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.search-results {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 16px;
  margin-top: 24px;
}

.card-result {
  display: grid;
  grid-template-columns: 120px 1fr;
  gap: 20px;
  padding: 16px;
  background: var(--surface-background);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  position: relative;
  overflow: hidden;
  transition: all 0.2s ease;
}

.card-image {
  position: relative;
  overflow: hidden;
  border-radius: 12px;
}

.card-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.quick-actions {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease;
}

.card-result:hover .quick-actions {
  opacity: 1;
}

.select-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  transform: translateY(10px);
  transition: all 0.3s ease;
}

.card-result:hover .select-button {
  transform: translateY(0);
}

.card-info {
  flex: 1;
}

.card-info h4 {
  margin: 0 0 8px 0;
  font-size: 16px;
  color: var(--text-primary);
}

.card-meta {
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 8px 0;
}

.set-name {
  font-size: 13px;
  color: var(--text-secondary);
}

.rarity {
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 500;
}

.rarity.common { background: var(--system-gray-alpha); color: var(--system-gray); }
.rarity.uncommon { background: var(--system-green-alpha); color: var(--system-green); }
.rarity.rare { background: var(--system-blue-alpha); color: var (--system-blue); }
.rarity.ultra { background: var(--system-purple-alpha); color: var(--system-purple); }
.rarity.secret { background: var(--system-yellow-alpha); color: var(--system-yellow); }

.price-info {
  display: flex;
  align-items: center;
  gap: 4px;
  color: var(--accent-primary);
  font-weight: 500;
}

/* Search States */
.search-loading,
.search-error,
.no-results {
  text-align: center;
  padding: 48px 24px;
  background: var(--surface-background);
  border-radius: 16px;
  border: 1px solid var(--card-border);
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid var(--card-border);
  border-top-color: var(--accent-primary);
  border-radius: 50%;
  margin: 0 auto 16px;
  animation: spin 1s linear infinite;
}

.loading-spinner.small {
  width: 20px;
  height: 20px;
  border-width: 2px;
  margin: 0;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Search Tips */
.search-tips {
  list-style: none;
  padding: 0;
  margin: 16px 0 0;
  text-align: left;
  display: inline-block;
}

.search-tips li {
  margin: 8px 0;
  color: var(--text-secondary);
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.search-tips li::before {
  content: "•";
  color: var(--accent-primary);
  font-weight: bold;
}

/* Load More Button */
.load-more {
  text-align: center;
  padding: 24px 0;
}

.load-more-button {
  padding: 12px 24px;
  background: var(--surface-background);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  color: var(--text-primary);
  font-weight: 500;
  transition: all 0.2s ease;
  min-width: 160px;
}

.load-more-button:hover:not(:disabled) {
  background: var(--background-elevated);
  transform: translateY(-2px);
}

/* Card Result Animation */
.card-result-enter-active,
.card-result-leave-active {
  transition: all 0.3s ease;
}

.card-result-enter-from {
  opacity: 0;
  transform: translateY(20px);
}

.card-result-leave-to {
  opacity: 0;
  transform: translateY(-20px);
}
</style>
