<template>
  <form @submit.prevent="handleSubmit" class="stock-form">
    <div class="form-section">
      <div class="product-info">
        <img :src="product.image_url" :alt="product.name">
        <div>
          <h3>{{ product.name }}</h3>
          <p>Current Stock: {{ product.stock }}</p>
        </div>
      </div>

      <div class="form-group">
        <label>Adjustment *</label>
        <div class="adjustment-input">
          <button 
            type="button" 
            class="adjust-button"
            :disabled="form.quantity <= -product.stock"
            @click="form.quantity--"
          >-</button>
          <input 
            v-model="form.quantity"
            type="number"
            required
          >
          <button 
            type="button" 
            class="adjust-button"
            @click="form.quantity++"
          >+</button>
        </div>
      </div>

      <div class="form-group">
        <label>Reason *</label>
        <select v-model="form.reason" required>
          <option value="restock">Restock</option>
          <option value="adjustment">Inventory Adjustment</option>
          <option value="damage">Damaged/Lost</option>
          <option value="other">Other</option>
        </select>
      </div>

      <div class="form-group" v-if="form.reason === 'other'">
        <label>Specify Reason</label>
        <input 
          v-model="form.customReason"
          type="text"
          placeholder="Enter reason"
          required
        >
      </div>
    </div>

    <div class="form-actions">
      <button type="button" class="secondary" @click="$emit('cancel')">
        Cancel
      </button>
      <button type="submit" class="primary" :disabled="isSubmitting">
        {{ isSubmitting ? 'Saving...' : 'Update Stock' }}
      </button>
    </div>
  </form>
</template>

<script>
import { ref } from 'vue'

export default {
  name: 'StockAdjustmentForm',
  props: {
    product: {
      type: Object,
      required: true
    }
  },
  emits: ['save', 'cancel'],
  setup(props) {
    const isSubmitting = ref(false)
    const form = ref({
      quantity: 0,
      reason: 'restock',
      customReason: ''
    })

    async function handleSubmit() {
      isSubmitting.value = true
      try {
        await this.$emit('save', {
          productId: props.product.id,
          quantity: form.value.quantity,
          reason: form.value.reason === 'other' 
            ? form.value.customReason 
            : form.value.reason
        })
      } finally {
        isSubmitting.value = false
      }
    }

    return {
      form,
      isSubmitting,
      handleSubmit
    }
  }
}
</script>

<style scoped>
/* Reuse form styles from ProductForm and add: */

.product-info {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px;
  border-bottom: 1px solid var(--card-border);
}

.product-info img {
  width: 60px;
  height: 60px;
  border-radius: 8px;
  object-fit: cover;
}

.product-info h3 {
  margin: 0 0 4px 0;
  font-size: 16px;
  color: var(--text-primary);
}

.product-info p {
  margin: 0;
  font-size: 14px;
  color: var(--text-secondary);
}

.adjustment-input {
  display: flex;
  align-items: center;
  gap: 12px;
}

.adjust-button {
  width: 36px;
  height: 36px;
  border-radius: 8px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
}

.adjust-button:hover:not(:disabled) {
  background: var(--background-elevated);
}

.adjustment-input input {
  width: 80px;
  text-align: center;
}
</style>
