<template>
  <div class="admin-container">
    <div class="admin-header">
      <h1>Admin Dashboard</h1>
      
      <!-- Quick Actions -->
      <div class="quick-actions">
        <button class="action-btn" @click="backupDatabase">
          <span class="material-symbols-rounded">backup</span>
          Backup Data
        </button>
        <button class="action-btn" @click="syncCards">
          <span class="material-symbols-rounded">sync</span>
          Sync Cards
        </button>
        <button class="action-btn warning" @click="toggleMaintenance">
          <span class="material-symbols-rounded">build</span>
          {{ settings.maintenanceMode ? 'Disable' : 'Enable' }} Maintenance
        </button>
      </div>

      <!-- Stats Grid -->
      <div class="quick-stats">
        <div class="stat-card users">
          <div class="stat-icon">
            <span class="material-symbols-rounded">group</span>
          </div>
          <div class="stat-info">
            <h3>Total Users</h3>
            <p>{{ stats.totalUsers }}</p>
            <span class="stat-change">
              +{{ stats.newToday }} today
            </span>
          </div>
        </div>
        
        <div class="stat-card orders">
          <div class="stat-icon">
            <span class="material-symbols-rounded">shopping_bag</span>
          </div>
          <div class="stat-info">
            <h3>Orders</h3>
            <p>{{ stats.totalOrders }}</p>
            <span class="stat-change">
              {{ stats.pendingOrders }} pending
            </span>
          </div>
        </div>
        
        <div class="stat-card revenue">
          <div class="stat-icon">
            <span class="material-symbols-rounded">payments</span>
          </div>
          <div class="stat-info">
            <h3>Revenue</h3>
            <p>€{{ formatMoney(stats.totalRevenue) }}</p>
            <span class="stat-change">
              €{{ formatMoney(stats.revenueToday) }} today
            </span>
          </div>
        </div>

        <div class="stat-card inventory">
          <div class="stat-icon">
            <span class="material-symbols-rounded">inventory_2</span>
          </div>
          <div class="stat-info">
            <h3>Inventory</h3>
            <p>{{ stats.totalCards }}</p>
            <span class="stat-change">
              {{ stats.lowStock }} low stock
            </span>
          </div>
        </div>
      </div>
    </div>

    <div class="admin-tabs">
      <button 
        v-for="tab in tabs" 
        :key="tab.id"
        :class="['tab-btn', { active: currentTab === tab.id }]"
        @click="currentTab = tab.id"
      >
        <span class="material-symbols-rounded">{{ tab.icon }}</span>
        {{ tab.name }}
        <span v-if="tab.badge" class="tab-badge">{{ tab.badge }}</span>
      </button>
    </div>

    <!-- Users Tab -->
    <div v-if="currentTab === 'users'" class="admin-panel">
      <div class="panel-header">
        <div class="search-box">
          <span class="material-symbols-rounded">search</span>
          <input 
            v-model="userSearch" 
            type="text" 
            placeholder="Search users..."
          >
        </div>
        <button class="add-button" @click="initNewUser">
          <span class="material-symbols-rounded">person_add</span>
          Add User
        </button>
      </div>

      <div class="table-container">
        <table class="data-table">
          <thead>
            <tr>
              <th>Email</th>
              <th>Name</th>
              <th>Role</th>
              <th>Status</th>
              <th>Joined</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="user in filteredUsers" :key="user.id">
              <td>{{ user.email }}</td>
              <td>{{ user.full_name || '-' }}</td>
              <td>
                <span class="role-badge" :class="user.role">
                  {{ user.role }}
                </span>
              </td>
              <td>
                <span class="status-badge" :class="user.status">
                  {{ user.status }}
                </span>
              </td>
              <td>{{ formatDate(user.created_at) }}</td>
              <td class="actions">
                <button @click="editUser(user)" class="icon-button">
                  <span class="material-symbols-rounded">edit</span>
                </button>
                <button @click="confirmDeleteUser(user)" class="icon-button danger">
                  <span class="material-symbols-rounded">delete</span>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- News Tab -->
    <div v-if="currentTab === 'news'" class="admin-panel">
      <NewsManager />
    </div>

    <!-- Cards Tab -->
    <div v-if="currentTab === 'cards'" class="admin-panel">
      <CardsManager />
    </div>

    <!-- Reports Tab -->
    <div v-if="currentTab === 'reports'" class="admin-panel">
      <ReportsManager />
    </div>

    <!-- Settings Tab -->
    <div v-if="currentTab === 'settings'" class="admin-panel">
      <SettingsManager />
    </div>

    <!-- Store Tab -->
    <div v-if="currentTab === 'store'" class="admin-panel">
      <StoreManager />
    </div>

    <div v-if="currentTab === 'orders'" class="admin-panel">
      <OrderManager />
    </div>

    <!-- Add Notifications Tab -->
    <div v-if="currentTab === 'notifications'" class="admin-panel">
      <NotificationsManager />
    </div>

    <!-- User Edit Modal -->
    <Modal v-if="showEditModal" @close="closeEditModal">
      <template #header>
        <h2>{{ editingUser && editingUser.id ? 'Edit User' : 'Add User' }}</h2>
      </template>
      <template #default>
        <form @submit.prevent="saveUser" class="edit-form">
          <div class="form-section">
            <!-- Email field - only shown for new users -->
            <div class="form-group" v-if="!(editingUser && editingUser.id)">
              <label>Email *</label>
              <div class="input-wrapper">
                <input 
                  type="email" 
                  v-model="userForm.email" 
                  required
                  placeholder="user@example.com"
                >
              </div>
            </div>
            
            <!-- Password field - only shown for new users -->
            <div class="form-group" v-if="!(editingUser && editingUser.id)">
              <label>Password *</label>
              <div class="input-wrapper">
                <input 
                  type="password" 
                  v-model="userForm.password" 
                  required
                  placeholder="Enter password"
                  minlength="6"
                >
              </div>
            </div>

            <div class="form-group">
              <label>First Name</label>
              <div class="input-wrapper">
                <input 
                  type="text" 
                  v-model="userForm.first_name"
                  placeholder="Enter first name"
                >
              </div>
            </div>

            <div class="form-group">
              <label>Last Name</label>
              <div class="input-wrapper">
                <input 
                  type="text" 
                  v-model="userForm.last_name"
                  placeholder="Enter last name"
                >
              </div>
            </div>

            <div class="form-group">
              <label>Role</label>
              <div class="select-wrapper">
                <select v-model="userForm.role">
                  <option value="user">Regular User</option>
                  <option value="admin">Administrator</option>
                </select>
                <span class="material-symbols-rounded">expand_more</span>
              </div>
            </div>

            <div class="form-group">
              <label>Status</label>
              <div class="select-wrapper">
                <select v-model="userForm.status">
                  <option value="active">Active</option>
                  <option value="suspended">Suspended</option>
                </select>
                <span class="material-symbols-rounded">expand_more</span>
              </div>
            </div>
          </div>

          <div v-if="formError" class="error-message">
            {{ formError }}
          </div>

          <div class="form-actions">
            <button type="button" class="cancel-button" @click="closeEditModal">
              Cancel
            </button>
            <button type="submit" class="save-button" :disabled="isSubmitting">
              {{ isSubmitting ? 'Saving...' : 'Save Changes' }}
            </button>
          </div>
        </form>
      </template>
    </Modal>

    <!-- Delete Confirmation Modal -->
    <Modal v-if="showDeleteModal" @close="showDeleteModal = false">
      <template #header>
        <h2>Confirm Delete</h2>
      </template>
      <template #default>
        <p>Are you sure you want to delete this user?</p>
        <div class="modal-actions">
          <button @click="showDeleteModal = false">Cancel</button>
          <button @click="deleteUser" class="danger">Delete</button>
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { supabase } from '@/services/supabase'
import Modal from '@/components/Modal.vue'
import NewsManager from '@/components/admin/NewsManager.vue'
import CardsManager from '@/components/admin/CardsManager.vue'
import ReportsManager from '@/components/admin/ReportsManager.vue'
import SettingsManager from '@/components/admin/SettingsManager.vue'
import StoreManager from '@/components/admin/StoreManager.vue'
import OrderManager from '@/components/admin/OrderManager.vue'
import NotificationsManager from '@/components/admin/NotificationsManager.vue'

export default {
  name: 'Admin',
  components: { 
    Modal,
    NewsManager,
    CardsManager,
    ReportsManager,
    SettingsManager,
    StoreManager,
    OrderManager,
    NotificationsManager
  },
  setup() {
    const currentTab = ref('users')
    const users = ref([])
    const userSearch = ref('')
    const showEditModal = ref(false)
    const showDeleteModal = ref(false)
    const editingUser = ref(null)
    const stats = ref({
      totalUsers: 0,
      newToday: 0,
      totalOrders: 0,
      pendingOrders: 0,
      totalRevenue: 0,
      revenueToday: 0,
      totalCards: 0,
      lowStock: 0
    })
    const settings = ref({
      maintenanceMode: false,
      allowRegistrations: true
    })

    const userForm = ref({
      email: '',
      password: '',
      first_name: '',
      last_name: '',
      role: 'user',
      status: 'active'
    })
    const formError = ref('')
    const isSubmitting = ref(false)

    const tabs = [
      { id: 'users', name: 'Users', icon: 'group' },
      { id: 'store', name: 'Store', icon: 'store' },
      { id: 'orders', name: 'Orders', icon: 'local_shipping' },
      { id: 'news', name: 'News', icon: 'newspaper' },
      { id: 'cards', name: 'Cards', icon: 'style' },
      { id: 'reports', name: 'Reports', icon: 'monitoring' },
      { id: 'settings', name: 'Settings', icon: 'settings' },
      { id: 'notifications', name: 'Notifications', icon: 'notifications_active' }
    ]

    const filteredUsers = computed(() => {
      if (!userSearch.value) return users.value
      const search = userSearch.value.toLowerCase()
      return users.value.filter(user => 
        user.email.toLowerCase().includes(search) ||
        (user.full_name && user.full_name.toLowerCase().includes(search))
      )
    })

    const formatMoney = (amount) => {
      return (amount / 100).toFixed(2)
    }

    const fetchStats = async () => {
      // Get today's stats
      const today = new Date()
      today.setHours(0, 0, 0, 0)

      try {
        // Fetch users stats
        const { count: newToday } = await supabase
          .from('profiles')
          .select('*', { count: 'exact' })
          .gte('created_at', today.toISOString())

        // Fetch orders stats
        const { data: ordersData, error: ordersError } = await supabase
          .from('orders')
          .select('status, total_amount, created_at')

        if (ordersError) {
          console.error('Error fetching orders:', ordersError)
          return
        }

        const orders = ordersData || []
        const pendingOrders = orders.filter(o => o.status === 'pending').length
        const todayOrders = orders.filter(o => new Date(o.created_at) >= today)
        const totalRevenue = orders.reduce((sum, order) => sum + (order.total_amount || 0), 0)
        const revenueToday = todayOrders.reduce((sum, order) => sum + (order.total_amount || 0), 0)

        // Fetch inventory stats
        const { count: lowStockCount } = await supabase
          .from('store_items')
          .select('*', { count: 'exact' })
          .lt('quantity', 5)

        // Update stats
        stats.value = {
          ...stats.value,
          newToday: newToday || 0,
          totalOrders: orders.length,
          pendingOrders,
          totalRevenue,
          revenueToday,
          lowStock: lowStockCount || 0
        }
      } catch (error) {
        console.error('Error fetching stats:', error)
      }
    }

    async function fetchUsers() {
      try {
        const { data, error } = await supabase
          .rpc('get_users_with_profiles')
    
        if (error) {
          console.error('Error fetching users:', error)
          return
        }
    
        console.log('Fetched users:', data) // Debug log
    
        users.value = data.map(user => ({
          id: user.id,
          email: user.email,
          full_name: user.first_name && user.last_name 
            ? `${user.first_name} ${user.last_name}`
            : '-',
          role: user.role || 'user',
          status: user.status || 'active',
          created_at: user.created_at
        }))
        
        stats.value.totalUsers = users.value.length
      } catch (err) {
        console.error('Unexpected error:', err)
      }
    }

    function initNewUser() {
      editingUser.value = null
      userForm.value = {
        email: '',
        password: '',
        first_name: '',
        last_name: '',
        role: 'user',
        status: 'active'
      }
      showEditModal.value = true
    }

    async function editUser(user) {
      const [firstName, lastName] = (user.full_name || '-').split(' ')
      editingUser.value = user
      userForm.value = {
        ...userForm.value,
        first_name: firstName !== '-' ? firstName : '',
        last_name: lastName || '',
        role: user.role || 'user',
        status: user.status || 'active'
      }
      showEditModal.value = true
    }

    async function confirmDeleteUser(user) {
      editingUser.value = user
      showDeleteModal.value = true
    }

    async function deleteUser() {
      if (!editingUser.value) return

      const { error } = await supabase
        .from('profiles')
        .delete()
        .eq('id', editingUser.value.id)

      if (error) {
        console.error('Error deleting user:', error)
        return
      }

      showDeleteModal.value = false
      editingUser.value = null
      await fetchUsers()
    }

    async function saveUser() {
      isSubmitting.value = true
      formError.value = ''

      try {
        if (editingUser.value && editingUser.value.id) {
          // Update existing user
          const { error } = await supabase
            .from('profiles')
            .update({
              first_name: userForm.value.first_name,
              last_name: userForm.value.last_name,
              role: userForm.value.role,
              status: userForm.value.status
            })
            .eq('id', editingUser.value.id)

          if (error) throw error
        } else {
          // Create new user
          const { data: authData, error: authError } = await supabase.auth.signUp({
            email: userForm.value.email,
            password: userForm.value.password
          })

          if (authError) throw authError

          // Create profile for new user
          const { error: profileError } = await supabase
            .from('profiles')
            .insert({
              id: authData.user.id,
              first_name: userForm.value.first_name,
              last_name: userForm.value.last_name,
              role: userForm.value.role,
              status: userForm.value.status
            })

          if (profileError) throw profileError
        }

        closeEditModal()
        await fetchUsers()
      } catch (error) {
        console.error('Error saving user:', error)
        formError.value = error.message
      } finally {
        isSubmitting.value = false
      }
    }

    async function saveSettings() {
      // Implement settings save logic
      console.log('Saving settings:', settings.value)
    }

    async function backupDatabase() {
      // Implement backup logic
      console.log('Backing up database...')
    }

    async function syncCards() {
      // Implement card sync logic
      console.log('Syncing cards...')
    }

    const toggleMaintenance = async () => {
      settings.value.maintenanceMode = !settings.value.maintenanceMode
      await saveSettings()
      notifications.info(`Maintenance mode ${settings.value.maintenanceMode ? 'enabled' : 'disabled'}`)
    }

    function closeEditModal() {
      showEditModal.value = false
      editingUser.value = null
      userForm.value = {
        email: '',
        password: '',
        first_name: '',
        last_name: '',
        role: 'user',
        status: 'active'
      }
      formError.value = ''
    }

    onMounted(async () => {
      await fetchUsers()
      await fetchStats()
    })

    return {
      currentTab,
      tabs,
      users,
      userSearch,
      filteredUsers,
      stats,
      settings,
      showEditModal,
      showDeleteModal,
      editingUser,
      userForm,
      formError,
      isSubmitting,
      editUser,
      confirmDeleteUser,
      deleteUser,
      saveUser,
      saveSettings,
      backupDatabase,
      syncCards,
      closeEditModal,
      initNewUser,
      formatDate: (date) => new Date(date).toLocaleDateString(),
      formatMoney,
      toggleMaintenance
    }
  }
}
</script>

<style scoped>
.admin-container {
  padding: 24px;
  max-width: 1440px;
  margin: 0 auto;
}

.admin-header {
  background: linear-gradient(135deg, var(--accent-primary), var(--system-purple));
  border-radius: 24px;
  padding: 32px;
  margin-bottom: 32px;
  color: white;
}

.admin-header h1 {
  margin: 0;
  font-size: 32px;
  font-weight: 700;
  letter-spacing: -0.02em;
}

.quick-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 24px;
  margin-top: 24px;
}

.stat-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 24px;
  display: flex;
  align-items: center;
  gap: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: transform 0.2s ease;
}

.stat-card:hover {
  transform: translateY(-2px);
}

.stat-card .material-symbols-rounded {
  font-size: 32px;
  color: white;
}

.stat-card h3 {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
  margin: 0;
  font-weight: 500;
}

.stat-card p {
  font-size: 28px;
  font-weight: 600;
  margin: 4px 0 0 0;
  color: white;
}

.admin-tabs {
  display: flex;
  gap: 12px;
  margin-bottom: 24px;
  padding: 8px;
  background: var(--surface-background);
  border-radius: 16px;
  border: 1px solid var(--card-border);
}

.admin-tabs button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  border: none;
  background: none;
  color: var(--text-secondary);
  border-radius: 12px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.admin-tabs button.active {
  background: var(--card-background);
  color: var(--text-primary);
  box-shadow: var(--shadow-sm);
}

.admin-panel {
  background: var(--card-background);
  border-radius: 20px;
  border: 1px solid var(--card-border);
  overflow: hidden;
}

.panel-header {
  padding: 24px;
  border-bottom: 1px solid var(--card-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-box {
  display: flex;
  align-items: center;
  gap: 12px;
  background: var(--surface-background);
  padding: 12px 16px;
  border-radius: 12px;
  width: 300px;
}

.search-box input {
  border: none;
  background: none;
  outline: none;
  font-size: 14px;
  color: var(--text-primary);
  width: 100%;
  cursor: text;
}

.add-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  background: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.add-button:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.data-table {
  width: 100%;
  border-collapse: collapse;
}

.data-table th {
  text-align: left;
  padding: 16px 24px;
  font-size: 13px;
  font-weight: 600;
  color: var(--text-secondary);
  background: var(--surface-background);
  border-bottom: 1px solid var(--card-border);
}

.data-table td {
  padding: 16px 24px;
  font-size: 14px;
  border-bottom: 1px solid var(--card-border);
}

.role-badge {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.role-badge.admin {
  background: var(--system-red);
  color: white;
}

.role-badge.user {
  background: var(--system-blue);
  color: white;
}

.status-badge {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 500;
}

.status-badge.active {
  background: var(--system-green);
  color: white;
}

.actions {
  display: flex;
  gap: 8px;
}

.icon-button {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--surface-background);
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.2s ease;
}

.icon-button:hover {
  background: var(--background-elevated);
}

.icon-button.danger:hover {
  background: var(--system-red);
  color: white;
}

@media (max-width: 768px) {
  .admin-header {
    padding: 24px;
  }
  
  .quick-stats {
    grid-template-columns: 1fr;
  }
  
  .panel-header {
    flex-direction: column;
    gap: 16px;
  }
  
  .search-box {
    width: 100%;
  }
}

/* Form Styles */
.edit-form {
  color: var(--text-primary);
}

.form-section {
  background: var(--surface-background);
  border-radius: 12px;
  border: 1px solid var(--card-border);
  overflow: hidden;
}

.form-group {
  padding: 16px;
  border-bottom: 1px solid var(--card-border);
}

.form-group:last-child {
  border-bottom: none;
}

.form-group label {
  display: block;
  font-size: 13px;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.input-display {
  font-size: 15px;
  color: var(--text-tertiary);
}

.select-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.select-wrapper .material-symbols-rounded {
  position: absolute;
  right: 12px;
  color: var(--text-secondary);
  pointer-events: none;
}

select {
  width: 100%;
  padding: 8px 12px;
  font-size: 15px;
  border: 1px solid var(--card-border);
  border-radius: 8px;
  background: var(--card-background);
  color: var(--text-primary);
  appearance: none;
  cursor: pointer;
}

select:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 2px var(--accent-primary-alpha);
}

select:disabled,
input:disabled,
button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}

button {
  cursor: pointer;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 24px;
}

.cancel-button {
