<template>
  <div class="store-manager">
    <!-- Stats Overview -->
    <div class="stats-grid">
      <div class="stat-card">
        <h3>Total Products</h3>
        <p class="stat-value">{{ stats.totalProducts }}</p>
        <div class="stat-trend up" v-if="stats.productTrend > 0">
          +{{ stats.productTrend }}% from last month
        </div>
      </div>
      <div class="stat-card">
        <h3>Total Sales</h3>
        <p class="stat-value">${{ stats.totalSales }}</p>
        <div class="stat-trend" :class="stats.salesTrend > 0 ? 'up' : 'down'">
          {{ stats.salesTrend > 0 ? '+' : '' }}{{ stats.salesTrend }}% from last month
        </div>
      </div>
      <div class="stat-card warning" v-if="stats.lowStock > 0">
        <h3>Low Stock Items</h3>
        <p class="stat-value">{{ stats.lowStock }}</p>
        <div class="stat-action">
          <button @click="showLowStock">View Items</button>
        </div>
      </div>
    </div>

    <!-- Actions Bar -->
    <div class="actions-bar">
      <div class="search-box">
        <span class="material-symbols-rounded">search</span>
        <input 
          v-model="search" 
          type="text" 
          placeholder="Search products..."
        >
      </div>
      <div class="action-buttons">
        <button class="secondary" @click="syncWithApi">
          <span class="material-symbols-rounded">sync</span>
          Sync with API
        </button>
        <button class="primary" @click="showAddProduct = true">
          <span class="material-symbols-rounded">add</span>
          Add Product
        </button>
      </div>
    </div>

    <!-- Bulk Actions Bar - Show when items are selected -->
    <div class="bulk-actions-bar" v-if="selectedProducts.length > 0">
      <div class="selection-info">
        {{ selectedProducts.length }} items selected
      </div>
      <div class="bulk-actions">
        <button class="danger" @click="confirmDeleteSelected">
          <span class="material-symbols-rounded">delete</span>
          Delete Selected
        </button>
      </div>
    </div>

    <!-- Products Table -->
    <div class="products-table">
      <table>
        <thead>
          <tr>
            <th>
              <input 
                type="checkbox" 
                :checked="isAllSelected"
                @change="toggleSelectAll"
              >
            </th>
            <th>Product</th>
            <th>Type</th>
            <th>CMP</th>
            <th>CSP</th>
            <th>Stock</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="product in filteredProducts" :key="product.id">
            <td>
              <input 
                type="checkbox"
                :value="product.id"
                v-model="selectedProducts"
              >
            </td>
            <td class="product-cell">
              <img :src="product.image_url" :alt="product.name">
              <div class="product-info">
                <h4>{{ product.name }}</h4>
                <p v-if="product.metadata && product.metadata.set">
                  {{ product.metadata.set.name }}
                </p>
              </div>
            </td>
            <td>{{ product.type }}</td>
            <td>${{ getMarketPrice(product) }}</td>
            <td>${{ product.price }}</td>
            <td :class="{ 'low-stock': product.stock < 5 }">
              {{ product.stock }}
            </td>
            <td>
              <span class="status-badge" :class="product.active ? 'active' : 'inactive'">
                {{ product.active ? 'Active' : 'Inactive' }}
              </span>
            </td>
            <td class="actions-cell">
              <button class="icon-button" @click="editProduct(product)">
                <span class="material-symbols-rounded">edit</span>
              </button>
              <button class="icon-button" @click="adjustStock(product)">
                <span class="material-symbols-rounded">inventory</span>
              </button>
              <button 
                class="icon-button"
                @click="toggleProductStatus(product)"
              >
                <span class="material-symbols-rounded">
                  {{ product.active ? 'visibility' : 'visibility_off' }}
                </span>
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Add/Edit Product Modal -->
    <Modal v-if="showAddProduct" @close="closeAddModal">
      <template #header>
        <h2>{{ editingProduct ? 'Edit Product' : 'Add Product' }}</h2>
      </template>
      <template #default>
        <ProductForm 
          :product="editingProduct"
          @save="saveProduct"
          @cancel="closeAddModal"
        />
      </template>
    </Modal>

    <!-- Stock Adjustment Modal -->
    <Modal v-if="showStockModal" @close="closeStockModal">
      <template #header>
        <h2>Adjust Stock</h2>
      </template>
      <template #default>
        <StockAdjustmentForm 
          :product="selectedProduct"
          @save="updateStock"
          @cancel="closeStockModal"
        />
      </template>
    </Modal>

    <!-- Delete Confirmation Modal -->
    <Modal v-if="showDeleteModal" @close="showDeleteModal = false">
      <template #header>
        <h2>Confirm Delete</h2>
      </template>
      <template #default>
        <div class="delete-confirmation">
          <p v-if="selectedProducts.length === filteredProducts.length">
            Are you sure you want to delete all products?
          </p>
          <p v-else>
            Are you sure you want to delete {{ selectedProducts.length }} selected products?
          </p>
          <div class="warning">
            <span class="material-symbols-rounded">warning</span>
            This action cannot be undone.
          </div>
          <div class="modal-actions">
            <button class="secondary" @click="showDeleteModal = false">
              Cancel
            </button>
            <button class="danger" @click="deleteSelected">
              Delete
            </button>
          </div>
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { supabase } from '@/services/supabase'
import pokemonApi from '@/services/pokemonApi' // Change this import
import Modal from '@/components/Modal.vue'
import ProductForm from './forms/ProductForm.vue'
import StockAdjustmentForm from './forms/StockAdjustmentForm.vue'

export default {
  name: 'StoreManager',
  components: { Modal, ProductForm, StockAdjustmentForm },
  setup() {
    const search = ref('')
    const products = ref([])
    const showAddProduct = ref(false)
    const showStockModal = ref(false)
    const editingProduct = ref(null)
    const selectedProduct = ref(null)
    const stats = ref({
      totalProducts: 0,
      totalSales: 0,
      lowStock: 0,
      productTrend: 0,
      salesTrend: 0
    })
    const selectedProducts = ref([])
    const showDeleteModal = ref(false)

    const filteredProducts = computed(() => {
      console.log('Filtering products:', products.value)
      if (!search.value) return products.value || []
      
      const searchTerm = search.value.toLowerCase()
      return (products.value || []).filter(product => {
        const nameMatch = product.name && product.name.toLowerCase().includes(searchTerm)
        const typeMatch = product.type && product.type.toLowerCase().includes(searchTerm)
        return nameMatch || typeMatch
      })
    })

    const isAllSelected = computed(() => {
      return filteredProducts.value.length > 0 && 
             selectedProducts.value.length === filteredProducts.value.length
    })

    async function fetchProducts() {
      try {
        const { data, error } = await supabase
          .from('products')
          .select(`
            id,
            name,
            description,
            price,
            stock,
            type,
            condition,
            tcg_id,
            image_url,
            admin_id,
            active,
            metadata,
            created_at,
            updated_at
          `)
          .order('created_at', { ascending: false })
        
        if (error) {
          console.error('Database error:', error)
          throw error
        }

        console.log('Raw products data:', data)

        products.value = (data || []).map(product => ({
          ...product,
          price: parseFloat(product.price) || 0,
          stock: parseInt(product.stock) || 0,
          active: typeof product.active === 'boolean' ? product.active : true,
          metadata: product.metadata || {},
          condition: product.condition || 'New',
          type: product.type || 'single'
        }))

        console.log('Transformed products:', products.value)
        updateStats()
      } catch (error) {
        console.error('Error fetching products:', error)
        alert('Failed to load products. Please try again.')
      }
    }

    function calculateTotalSales() {
      // TODO: Implement proper sales calculation
      return products.value.reduce((total, product) => {
        return total + (product.price * (product.total_sold || 0))
      }, 0).toFixed(2)
    }

    function calculateProductTrend() {
      // TODO: Implement proper trend calculation
      return 0
    }

    function calculateSalesTrend() {
      // TODO: Implement proper trend calculation
      return 0
    }

    function updateStats() {
      stats.value = {
        totalProducts: products.value.length,
        totalSales: calculateTotalSales(),
        lowStock: products.value.filter(p => p.stock < 5).length,
        productTrend: calculateProductTrend(),
        salesTrend: calculateSalesTrend()
      }
    }

    async function syncWithApi() {
      try {
        const { data } = await pokemonApi.getCards()
        
        if (!data) {
          throw new Error('No data received from API')
        }
        
        const newProducts = data.map(card => {
          const cardPrice = card.cardmarket && 
            card.cardmarket.prices && 
            card.cardmarket.prices.averageSellPrice || 0;

          return {
            name: card.name,
            type: 'single',
            tcg_id: card.id,
            price: cardPrice,
            stock: 0,
            image_url: card.images.large,
            active: true,
            metadata: {
              set: card.set,
              rarity: card.rarity,
              number: card.number,
              artist: card.artist
            }
          }
        })

        // Insert new products into database
        const { error } = await supabase
          .from('products')
          .upsert(
            newProducts,
            { 
              onConflict: 'tcg_id',
              ignoreDuplicates: false
            }
          )

        if (error) throw error

        // Refresh products list
        await fetchProducts()
      } catch (error) {
        console.error('Error syncing with API:', error)
        alert('Failed to sync products with API. Please try again.')
      }
    }

    async function saveProduct(productData) {
      try {
        const formattedData = {
          name: productData.name,
          description: productData.description || '',
          price: parseFloat(productData.price) || 0,
          stock: parseInt(productData.stock) || 0,
          type: productData.type || 'single',
          condition: productData.condition || 'New',
          tcg_id: productData.tcg_id || null,
          image_url: productData.image_url || '',
          active: typeof productData.active === 'boolean' ? productData.active : true,
          metadata: productData.metadata || {},
          updated_at: new Date().toISOString()
        }

        console.log('Saving product with data:', formattedData)

        if (editingProduct.value) {
          const { data, error } = await supabase
            .from('products')
            .update(formattedData)
            .eq('id', editingProduct.value.id)
            .select()

          if (error) throw error
          console.log('Updated product:', data)
        } else {
          const { data, error } = await supabase
            .from('products')
            .insert([{
              ...formattedData,
              created_at: new Date().toISOString()
            }])
            .select()

          if (error) throw error
          console.log('Created product:', data)
        }

        await fetchProducts()
        closeAddModal()
      } catch (error) {
        console.error('Error saving product:', error)
        alert('Failed to save product: ' + error.message)
      }
    }

    function closeAddModal() {
      showAddProduct.value = false
      editingProduct.value = null
    }

    function closeStockModal() {
      showStockModal.value = false
      selectedProduct.value = null
    }

    async function updateStock({ productId, quantity, reason }) {
      await supabase
        .from('stock_movements')
        .insert({
          product_id: productId,
          quantity,
          reason
        })

      await fetchProducts()
      closeStockModal()
    }

    // Modified function to handle product editing
    function editProduct(product) {
      console.log('Editing product:', product) // Debug log
      editingProduct.value = { ...product }
      showAddProduct.value = true
    }

    // Add function to handle stock adjustments
    function adjustStock(product) {
      console.log('Adjusting stock for:', product) // Debug log
      selectedProduct.value = product
      showStockModal.value = true
    }

    // Add function to toggle product status
    async function toggleProductStatus(product) {
      try {
        const { error } = await supabase
          .from('products')
          .update({ active: !product.active })
          .eq('id', product.id)

        if (error) throw error
        await fetchProducts()
      } catch (error) {
        console.error('Error toggling product status:', error)
        alert('Failed to update product status')
      }
    }

    function getMarketPrice(product) {
      if (!product.metadata) return '0.00';
      
      const tcgPrice = product.metadata.tcg_pricing && 
                      product.metadata.tcg_pricing.market;
      
      const marketPrice = product.metadata.cardmarket && 
                         product.metadata.cardmarket.prices && 
                         product.metadata.cardmarket.prices.averageSellPrice;
      
      const price = tcgPrice || marketPrice || 0;
      return price.toFixed(2);
    }

    function toggleSelectAll(event) {
      if (event.target.checked) {
        selectedProducts.value = filteredProducts.value.map(p => p.id)
      } else {
        selectedProducts.value = []
      }
    }

    function confirmDeleteSelected() {
      showDeleteModal.value = true
    }

    async function deleteSelected() {
      try {
        const { error } = await supabase
          .from('products')
          .delete()
          .in('id', selectedProducts.value)

        if (error) throw error

        await fetchProducts()
        selectedProducts.value = []
        showDeleteModal.value = false
      } catch (error) {
        console.error('Error deleting products:', error)
        alert('Failed to delete products: ' + error.message)
      }
    }

    onMounted(() => {
      console.log('StoreManager mounted')
      fetchProducts()
    })
    fetchProducts() // Initial fetch

    return {
      search,
      filteredProducts, // Return the computed property directly
      products,
      stats,
      showAddProduct,
      showStockModal,
      editingProduct,
      selectedProduct,
      syncWithApi,
      saveProduct,
      closeAddModal,
      closeStockModal,
      updateStock,
      calculateTotalSales,
      calculateProductTrend,
      calculateSalesTrend,
      editProduct,
      adjustStock,
      toggleProductStatus,
      getMarketPrice,
      selectedProducts,
      isAllSelected,
      showDeleteModal,
      toggleSelectAll,
      confirmDeleteSelected,
      deleteSelected
    }
  }
}
</script>

<style scoped>
.store-manager {
  padding: 24px;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 24px;
  margin-bottom: 32px;
}

.stat-card {
  background: var(--card-background);
  border-radius: 16px;
  padding: 24px;
  border: 1px solid var(--card-border);
}

.stat-card h3 {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0 0 8px 0;
}

.stat-value {
  font-size: 32px;
  font-weight: 700;
  margin: 0;
  color: var(--text-primary);
}

.stat-trend {
  margin-top: 12px;
  font-size: 13px;
  display: flex;
  align-items: center;
  gap: 4px;
}

.stat-trend.up { color: var(--system-green); }
.stat-trend.down { color: var(--system-red); }

.actions-bar {
  display: flex;
  gap: 16px;
  margin-bottom: 24px;
  align-items: center;
}

.search-box {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 12px;
  background: var(--surface-background);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  padding: 12px 16px;
}

.search-box input {
  border: none;
  background: none;
  outline: none;
  width: 100%;
  font-size: 14px;
  color: var(--text-primary);
}

.action-buttons {
  display: flex;
  gap: 12px;
}

.action-buttons button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
}

.action-buttons button.secondary {
  background: var(--surface-background);
  color: var(--text-primary);
  border: 1px solid var(--card-border);
}

.action-buttons button.primary {
  background: var(--accent-primary);
  color: white;
}

.bulk-actions-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  background: var(--surface-background);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  margin-bottom: 16px;
}

.selection-info {
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
}

.bulk-actions {
  display: flex;
  gap: 12px;
}

.bulk-actions button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.bulk-actions button.danger {
  background: var(--system-red);
  color: white;
  border: none;
}

.bulk-actions button.danger:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.products-table {
  background: var(--card-background);
  border-radius: 16px;
  border: 1px solid var(--card-border);
  overflow: hidden;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th {
  text-align: left;
  padding: 16px;
  font-size: 13px;
  font-weight: 600;
  color: var(--text-secondary);
  background: var(--surface-background);
  border-bottom: 1px solid var(--card-border);
}

td {
  padding: 16px;
  font-size: 14px;
  color: var(--text-primary);
  border-bottom: 1px solid var(--card-border);
}

.product-cell {
  display: flex;
  align-items: center;
  gap: 12px;
}

.product-cell img {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  object-fit: cover;
}

.product-info h4 {
  margin: 0;
  font-size: 14px;
  font-weight: 500;
}

.product-info p {
  margin: 4px 0 0 0;
  font-size: 13px;
  color: var(--text-secondary);
}

.status-badge {
  padding: 4px 8px;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 500;
}

.status-badge.active {
  background: var(--system-green-alpha);
  color: var(--system-green);
}

.status-badge.inactive {
  background: var(--system-red-alpha);
  color: var(--system-red);
}

.actions-cell {
  display: flex;
  gap: 8px;
}

.icon-button {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  border: none;
  background: var(--surface-background);
  color: var(--text-secondary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.icon-button:hover {
  background: var(--background-elevated);
  color: var(--text-primary);
  transform: translateY(-1px);
}

.delete-confirmation {
  padding: 24px;
}

.delete-confirmation .warning {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 16px;
  padding: 12px;
  background: var(--system-red-alpha);
  color: var(--system-red);
  border-radius: 8px;
  font-size: 14px;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 24px;
}

input[type="checkbox"] {
  width: 18px;
  height: 18px;
  border-radius: 4px;
  border: 1px solid var(--card-border);
  cursor: pointer;
}

/* Dark mode adjustments */
@media (prefers-color-scheme: dark) {
  .stat-card,
  .products-table {
    backdrop-filter: blur(20px);
  }
}
</style>
