<template>
  <header class="app-header">
    <nav class="nav-container">
      <!-- Logo and main nav items -->
      <div class="nav-left">
        <router-link to="/" class="logo">
          <span class="material-symbols-rounded">style</span>
          CardWatcher
        </router-link>
        
        <div class="nav-links">
          <router-link to="/collection">Browse</router-link>
          <!-- <router-link to="/pricing">Market Prices</router-link> -->
          <router-link to="/store">Store</router-link>
          <!-- Only show these links when logged in -->
          <template v-if="isLoggedIn">
            <router-link to="/my-collection">My Collection</router-link>
            <router-link to="/wishlist">Wishlist</router-link>
          </template>
        </div>
      </div>

      <!-- Auth buttons -->
      <div class="nav-right">
        <!-- Cart Button -->
        <router-link to="/cart" class="cart-button">
          <span class="material-symbols-rounded">shopping_cart</span>
          <span v-if="cart.state.items.length" class="cart-badge">
            {{ cart.state.items.length }}
          </span>
        </router-link>

        <!-- Notification Bell with Dropdown -->
        <div class="notification-menu">
          <button class="nav-button notification-button" @click="toggleNotifications">
            <span class="material-symbols-rounded">notifications</span>
            <span v-if="unreadNotifications" class="notification-badge">
              {{ unreadNotifications }}
            </span>
          </button>
          
          <!-- Notifications Dropdown -->
          <div v-if="showNotifications" class="notifications-dropdown">
            <div class="dropdown-header">
              <h3>Notifications</h3>
              <button v-if="notifications.length" @click="clearNotifications" class="clear-button">
                Clear All
              </button>
            </div>
            
            <div class="notifications-list">
              <div v-if="!notifications.length" class="empty-notifications">
                <span class="material-symbols-rounded">notifications_off</span>
                <p>No notifications</p>
              </div>
              
              <div v-for="notification in notifications" 
                   :key="notification.id"
                   :class="['notification-item', notification.type]">
                <span class="icon material-symbols-rounded">
                  {{ getNotificationIcon(notification.type) }}
                </span>
                <div class="content">
                  <p>{{ notification.message }}</p>
                  <span class="timestamp">{{ formatTime(notification.timestamp) }}</span>
                </div>
                <button class="dismiss-button" @click.stop="dismissNotification(notification.id)">
                  <span class="material-symbols-rounded">close</span>
                </button>
              </div>
            </div>
          </div>
        </div>

        <template v-if="isLoggedIn">
          <button class="user-menu" @click="toggleUserMenu">
            <span class="material-symbols-rounded">account_circle</span>
            <span class="username">
              {{ userDisplayName }}
              <span class="role-badge" :class="userRole">{{ userRole }}</span>
            </span>
            <span class="material-symbols-rounded">expand_more</span>
          </button>
          <!-- Dropdown menu -->
          <div v-if="showUserMenu" class="user-dropdown">
            <router-link to="/profile" class="dropdown-item">
              <span class="material-symbols-rounded">person</span>
              Profile
            </router-link>
            <router-link to="/orders" class="dropdown-item">
              <span class="material-symbols-rounded">receipt_long</span>
              My Orders
            </router-link>
            <router-link to="/admin" class="dropdown-item" v-if="isAdmin">
              <span class="material-symbols-rounded">admin_panel_settings</span>
              Admin Panel
            </router-link>
            <button @click="handleSignOut" class="dropdown-item">
              <span class="material-symbols-rounded">logout</span>
              Sign Out
            </button>
          </div>
        </template>
        <template v-else>
          <router-link to="/register" class="auth-button secondary">Register</router-link>
          <router-link to="/login" class="auth-button primary">
            <span class="material-symbols-rounded">login</span>
            Sign In
          </router-link>
        </template>
      </div>

      <!-- Mobile Menu Toggle -->
      <button v-if="isMobile" class="mobile-menu-btn" @click="toggleMobileNav">
        <span class="material-symbols-rounded">{{ showMobileNav ? 'close' : 'menu' }}</span>
      </button>
    </nav>

    <!-- Add Cart SlideOver -->
    <CartSlideOver :show="showCart" @close="showCart = false" />

    <!-- Mobile Menu -->
    <transition name="slide">
      <div v-if="showMobileNav" class="mobile-menu">
        <div class="mobile-menu-header">
          <router-link to="/" class="logo" @click="toggleMobileNav">
            <span class="material-symbols-rounded">style</span>
            CardWatcher
          </router-link>
          <button @click="toggleMobileNav" class="close-button">
            <span class="material-symbols-rounded">close</span>
          </button>
        </div>
        <div class="mobile-menu-content">
          <div class="mobile-menu-section">
            <router-link to="/collection" @click="toggleMobileNav" class="mobile-menu-item">
              <span class="material-symbols-rounded">dashboard</span>
              Browse
            </router-link>
            <router-link to="/store" @click="toggleMobileNav" class="mobile-menu-item">
              <span class="material-symbols-rounded">store</span>
              Store
            </router-link>
            <router-link to="/cart" @click="toggleMobileNav" class="mobile-menu-item">
              <span class="material-symbols-rounded">shopping_cart</span>
              Cart
              <span v-if="cart.state.items.length" class="mobile-badge">{{ cart.state.items.length }}</span>
            </router-link>
          </div>

          <template v-if="isLoggedIn">
            <div class="mobile-menu-section">
              <div class="section-title">My Account</div>
              <router-link to="/my-collection" @click="toggleMobileNav" class="mobile-menu-item">
                <span class="material-symbols-rounded">collections_bookmark</span>
                My Collection
              </router-link>
              <router-link to="/wishlist" @click="toggleMobileNav" class="mobile-menu-item">
                <span class="material-symbols-rounded">favorite</span>
                Wishlist
              </router-link>
              <router-link to="/profile" @click="toggleMobileNav" class="mobile-menu-item">
                <span class="material-symbols-rounded">person</span>
                Profile
              </router-link>
              <router-link to="/orders" @click="toggleMobileNav" class="mobile-menu-item">
                <span class="material-symbols-rounded">receipt_long</span>
                Orders
              </router-link>
              <router-link v-if="isAdmin" to="/admin" @click="toggleMobileNav" class="mobile-menu-item">
                <span class="material-symbols-rounded">admin_panel_settings</span>
                Admin Panel
              </router-link>
            </div>
            <div class="mobile-menu-footer">
              <button @click="handleSignOut" class="mobile-menu-item sign-out">
                <span class="material-symbols-rounded">logout</span>
                Sign Out
              </button>
            </div>
          </template>
          <template v-else>
            <div class="mobile-menu-section auth-section">
              <router-link to="/login" @click="toggleMobileNav" class="auth-button primary">
                <span class="material-symbols-rounded">login</span>
                Sign In
              </router-link>
              <router-link to="/register" @click="toggleMobileNav" class="auth-button secondary">
                Register
              </router-link>
            </div>
          </template>
        </div>
      </div>
    </transition>

    <!-- Overlay -->
    <transition name="fade">
      <div v-if="showMobileNav" class="mobile-menu-overlay" @click="toggleMobileNav"></div>
    </transition>
  </header>
</template>

<script>
import auth from '@/store/auth'
import cart from '@/store/cart'
import { ref, computed, watch, onMounted } from 'vue'
import CartSlideOver from './CartSlideOver.vue'
import { useRouter } from 'vue-router'
import notifications from '@/store/notifications'

export default {
  name: 'AppHeader',
  components: {
    CartSlideOver
  },
  setup() {
    const showUserMenu = ref(false)

    const isLoggedIn = computed(() => !!auth.state.session)
    const userDisplayName = computed(() => {
      if (!auth.state.user) return ''
      const profile = auth.state.profile
      if (profile && profile.first_name && profile.last_name) {
        return `${profile.first_name} ${profile.last_name}`
      }
      return auth.state.user.email
    })

    const isAdmin = computed(() => auth.state.isAdmin)

    const userRole = computed(() => {
      return auth.state.isAdmin ? 'admin' : 'user'
    })

    const toggleUserMenu = () => {
      showUserMenu.value = !showUserMenu.value
    }

    const handleSignOut = async () => {
      await auth.signOut()
      showUserMenu.value = false
    }

    const router = useRouter()

    // Watch cart items for badge animation
    watch(() => cart.state.items.length, (newValue, oldValue) => {
      if (newValue > oldValue) {
        const badge = document.querySelector('.cart-badge')
        if (badge) {
          badge.classList.add('pulse')
          setTimeout(() => badge.classList.remove('pulse'), 300)
        }
      }
    })

    // Add notification related code
    const unreadNotifications = computed(() => {
      return notifications.state.notifications.length
    })

    const showNotifications = ref(false)
    const notificationsList = computed(() => notifications.state.notifications)

    const toggleNotifications = () => {
      showNotifications.value = !showNotifications.value
    }

    const dismissNotification = (id) => {
      notifications.remove(id)
    }

    const clearNotifications = () => {
      notifications.clear()
    }

    const getNotificationIcon = (type) => {
      const icons = {
        success: 'check_circle',
        error: 'error',
        warning: 'warning',
        info: 'info'
      }
      return icons[type] || 'notifications'
    }

    const formatTime = (timestamp) => {
      return new Date(timestamp).toLocaleTimeString()
    }

    // Close notifications dropdown when clicking outside
    onMounted(() => {
      document.addEventListener('click', (e) => {
        if (!e.target.closest('.notification-menu')) {
          showNotifications.value = false
        }
      })
    })

    // Mobile navigation
    const showMobileNav = ref(false)
    const toggleMobileNav = () => {
      showMobileNav.value = !showMobileNav.value
    }

    const isMobile = ref(false)

    const checkMobile = () => {
      isMobile.value = window.innerWidth <= 768
    }

    onMounted(() => {
      checkMobile()
      window.addEventListener('resize', checkMobile)
    })

    return {
      isLoggedIn,
      showUserMenu,
      toggleUserMenu,
      handleSignOut,
      userDisplayName,
      isAdmin,
      userRole,
      cart,
      unreadNotifications,
      toggleNotifications,
      showNotifications,
      notifications: notificationsList,
      dismissNotification,
      clearNotifications,
      getNotificationIcon,
      formatTime,
      showMobileNav,
      toggleMobileNav,
      isMobile
    }
  }
}
</script>

<style scoped>
.app-header {
  background: var(--card-background);
  backdrop-filter: var(--blur-md);
  -webkit-backdrop-filter: var(--blur-md);
  border-bottom: 1px solid var(--card-border);
  position: sticky;
  top: 0;
  z-index: 100;
}

.nav-container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 16px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-left {
  display: flex;
  align-items: center;
  gap: 48px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 20px;
  font-weight: 600;
  color: var(--text-primary);
  text-decoration: none;
}

.nav-links {
  display: flex;
  gap: 24px;
}

.nav-links a {
  color: var(--text-secondary);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
}

.nav-links a:hover {
  color: var(--text-primary);
}

.nav-links a.router-link-active {
  color: var(--accent-primary);
}

.nav-right {
  position: relative;
  display: flex;
  align-items: center;
  gap: 12px;
}

.auth-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border-radius: 8px;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.2s ease;
}

.auth-button.primary {
  background: var(--accent-primary);
  color: white;
}

.auth-button.secondary {
  background: var(--surface-background);
  color: var(--text-primary);
}

.user-menu {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: var(--surface-background);
  border: none;
  border-radius: 8px;
  color: var(--text-primary);
  cursor: pointer;
}

.user-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 8px;
  background: var(--card-background);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  padding: 8px;
  min-width: 200px;
  box-shadow: var(--shadow-lg);
}

.dropdown-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  width: 100%;
  border: none;
  background: none;
  color: var(--text-primary);
  font-size: 14px;
  text-decoration: none;
  cursor: pointer;
  border-radius: 6px;
}

.dropdown-item:hover {
  background: var(--surface-background);
}

.role-badge {
  display: inline-block;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  margin-left: 8px;
}

.role-badge.admin {
  background: var(--system-red);
  color: white;
}

.role-badge.user {
  background: var(--system-blue);
  color: white;
}

.cart-button {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  border: none;
  background: var(--surface-background);
  color: var(--text-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  text-decoration: none; /* Add this to remove link underline */
}

.cart-button:hover {
  background: var(--background-elevated);
  transform: translateY(-2px);
}

.cart-badge {
  position: absolute;
  top: -6px;
  right: -6px;
  background: var(--accent-primary);
  color: white;
  font-size: 12px;
  font-weight: 600;
  padding: 2px 6px;
  border-radius: 10px;
  min-width: 18px;
  text-align: center;
  animation: none;
}

.cart-badge.pulse {
  animation: pulse 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.5); }
  100% { transform: scale(1); }
}

/* Ensure cart slide-over appears above header */
:deep(.cart-overlay) {
  z-index: var(--z-index-cart);
}

.notification-button {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  border: none;
  background: var(--surface-background);
  color: var(--text-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.notification-button:hover {
  background: var(--background-elevated);
  transform: translateY(-2px);
}

.notification-badge {
  position: absolute;
  top: -6px;
  right: -6px;
  background: var(--system-red);
  color: white;
  font-size: 12px;
  font-weight: 600;
  padding: 2px 6px;
  border-radius: 10px;
  min-width: 18px;
  text-align: center;
}

/* Add pulse animation for new notifications */
@keyframes notification-pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

.notification-badge.new {
  animation: notification-pulse 0.3s ease;
}

.notification-menu {
  position: relative;
}

.notifications-dropdown {
  position: absolute;
  top: calc(100% + 8px);
  right: 0;
  width: 360px;
  background: var(--card-background);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  box-shadow: var(--shadow-lg);
  overflow: hidden;
  animation: slideDown 0.2s ease-out;
}

.dropdown-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid var(--card-border);
}

.dropdown-header h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
}

.clear-button {
  padding: 4px 8px;
  border: none;
  background: var(--surface-background);
  color: var(--text-secondary);
  border-radius: 6px;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.clear-button:hover {
  color: var(--text-primary);
  background: var(--background-elevated);
}

.notifications-list {
  max-height: 400px;
  overflow-y: auto;
}

.notification-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 12px 16px;
  border-bottom: 1px solid var(--card-border);
  transition: background-color 0.2s ease;
}

.notification-item:hover {
  background: var(--surface-background);
}

.notification-item .icon {
  padding-top: 2px;
}

.notification-item.success .icon { color: var(--system-green); }
.notification-item.error .icon { color: var(--system-red); }
.notification-item.warning .icon { color: var(--system-orange); }
.notification-item.info .icon { color: var(--system-blue); }

.notification-item .content {
  flex: 1;
}

.notification-item .content p {
  margin: 0;
  font-size: 14px;
  line-height: 1.4;
}

.notification-item .timestamp {
  display: block;
  font-size: 12px;
  color: var(--text-tertiary);
  margin-top: 4px;
}

.dismiss-button {
  padding: 4px;
  border: none;
  background: none;
  color: var(--text-tertiary);
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  transition: all 0.2s ease;
}

.notification-item:hover .dismiss-button {
  opacity: 1;
}

.dismiss-button:hover {
  background: var(--background-elevated);
  color: var(--text-primary);
}

.empty-notifications {
  padding: 32px;
  text-align: center;
  color: var(--text-tertiary);
}

.empty-notifications .material-symbols-rounded {
  font-size: 32px;
  margin-bottom: 8px;
}

.empty-notifications p {
  margin: 0;
  font-size: 14px;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .nav-links, 
  .nav-right {
    display: none;
  }
  
  .username {
    display: none;
  }

  .notifications-dropdown {
    position: fixed;
    top: var(--navbar-height);
    left: 16px;
    right: 16px;
    width: auto;
    max-width: none;
  }

  .mobile-menu-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    color: var(--text-primary);
    font-size: 24px;
    cursor: pointer;
    padding: 8px;
    margin-left: auto;
  }

  .mobile-menu {
    position: fixed;
    top: var(--navbar-height, 64px);
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--card-background);
    z-index: 1000;
    overflow-y: auto;
  }

  .mobile-menu.is-active {
    transform: translateX(0);
  }

  .mobile-menu-content {
    display: flex;
    flex-direction: column;
    padding: 24px;
    gap: 16px;
    height: 100%;
    overflow-y: auto;
  }

  .mobile-menu-content a,
  .mobile-menu-content button {
    font-size: 18px;
    color: var(--text-primary);
    text-decoration: none;
    background: none;
    border: none;
    padding: 12px;
    text-align: left;
    cursor: pointer;
    border-radius: 8px;
  }

  .mobile-menu-content a:hover,
  .mobile-menu-content button:hover {
    background: var(--surface-background);
  }

  .mobile-badge {
    background: var(--accent-primary);
    color: white;
    padding: 2px 8px;
    border-radius: 12px;
    font-size: 14px;
  }
}

/* Slide transition */
.slide-enter-active,
.slide-leave-active {
  transition: transform 0.3s ease;
}

.slide-enter-from,
.slide-leave-to {
  transform: translateX(100%);
}

.mobile-menu {
  position: fixed;
  top: 0;
  right: 0;
  width: min(400px, 100%);
  height: 100vh;
  background: var(--card-background);
  z-index: 1001;
  display: flex;
  flex-direction: column;
  box-shadow: var(--shadow-xl);
}

.mobile-menu-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  border-bottom: 1px solid var(--card-border);
}

.mobile-menu-content {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.mobile-menu-section {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.section-title {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-tertiary);
  padding: 0 12px;
  margin-bottom: 4px;
}

.mobile-menu-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  color: var(--text-primary);
  text-decoration: none;
  border-radius: 8px;
  transition: all 0.2s ease;
  font-weight: 500;
}

.mobile-menu-item:hover {
  background: var(--surface-background);
}

.mobile-menu-item.sign-out {
  color: var(--system-red);
  width: 100%;
  border: none;
  background: none;
  cursor: pointer;
  text-align: left;
  font-size: 16px;
}

.mobile-badge {
  margin-left: auto;
  background: var(--accent-primary);
  color: white;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 14px;
}

.mobile-menu-footer {
  margin-top: auto;
  padding-top: 16px;
  border-top: 1px solid var(--card-border);
}

.auth-section {
  padding: 16px;
  gap: 12px;
}

.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  z-index: 1000;
}

/* Transitions */
.slide-enter-active,
.slide-leave-active {
  transition: transform 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

.slide-enter-from,
.slide-leave-to {
  transform: translateX(100%);
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
