<template>
  <div class="order-manager">
    <!-- Enhanced Panel Header -->
    <div class="panel-header">
      <div class="header-content">
        <h2>Order Management</h2>
        <p class="subtitle">Manage and track customer orders</p>
      </div>
      
      <!-- Advanced Filters -->
      <div class="filters">
        <div class="search-box">
          <span class="material-symbols-rounded">search</span>
          <input v-model="search" type="text" placeholder="Search by order ID, customer name or email...">
        </div>
        <div class="filter-group">
          <select v-model="statusFilter" class="filter-select">
            <option value="">All Statuses</option>
            <option v-for="status in orderStatuses" 
                    :key="status.value" 
                    :value="status.value"
                    :class="status.value">
              {{ status.label }}
            </option>
          </select>
          <select v-model="timeFilter" class="filter-select">
            <option value="all">All Time</option>
            <option value="today">Today</option>
            <option value="week">This Week</option>
            <option value="month">This Month</option>
          </select>
          <button class="filter-button" @click="clearFilters">
            <span class="material-symbols-rounded">filter_alt_off</span>
            Clear Filters
          </button>
        </div>
      </div>

      <!-- Enhanced Stats Cards -->
      <div class="stat-pills">
        <div class="stat-pill" :class="{ active: statusFilter === 'pending' }">
          <div class="stat-icon">
            <span class="material-symbols-rounded">schedule</span>
          </div>
          <div class="stat-content">
            <span>Pending Orders</span>
            <strong>{{ pendingCount }}</strong>
          </div>
        </div>
        <div class="stat-pill" :class="{ active: statusFilter === 'approved' }">
          <div class="stat-icon">
            <span class="material-symbols-rounded">check_circle</span>
          </div>
          <div class="stat-content">
            <span>Approved</span>
            <strong>{{ approvedCount }}</strong>
          </div>
        </div>
        <div class="stat-pill" :class="{ active: statusFilter === 'preparing' }">
          <div class="stat-icon">
            <span class="material-symbols-rounded">inventory</span>
          </div>
          <div class="stat-content">
            <span>In Preparation</span>
            <strong>{{ preparingCount }}</strong>
          </div>
        </div>
        <div class="stat-pill success">
          <div class="stat-icon">
            <span class="material-symbols-rounded">payments</span>
          </div>
          <div class="stat-content">
            <span>Total Revenue</span>
            <strong>€{{ totalRevenue }}</strong>
          </div>
        </div>
      </div>
    </div>

    <!-- Enhanced Table -->
    <div class="table-container" :class="{ 'is-loading': isLoading }">
      <div v-if="isLoading" class="loading-overlay">
        <span class="material-symbols-rounded spin">sync</span>
        Loading orders...
      </div>
      
      <table class="data-table" v-else>
        <thead>
          <tr>
            <th>Order ID</th>
            <th>Customer</th>
            <th>Items</th>
            <th>Total</th>
            <th>Status</th>
            <th>Date</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="order in filteredOrders" :key="order.id">
            <td>#{{ order.id.slice(0,8) }}</td>
            <td>
              <div class="customer-info">
                <span>{{ order.customer_name }}</span>
                <small>{{ order.customer_email }}</small>
              </div>
            </td>
            <td>{{ order.items.length }} items</td>
            <td class="price">€{{ formatPrice(order.total_amount) }}</td>
            <td>
              <select v-model="order.status" 
                      @change="updateOrderStatus(order)"
                      :class="['status-select', order.status]">
                <option v-for="status in orderStatuses" 
                        :key="status.value"
                        :value="status.value">
                  {{ status.label }}
                </option>
              </select>
            </td>
            <td>{{ formatDate(order.created_at) }}</td>
            <td class="actions">
              <button @click="viewOrderDetails(order)" class="icon-button">
                <span class="material-symbols-rounded">visibility</span>
              </button>
              <button @click="downloadInvoice(order)" class="icon-button">
                <span class="material-symbols-rounded">receipt_long</span>
              </button>
            </td>
          </tr>
        </tbody>
      </table>

      <!-- Empty State -->
      <div v-if="!isLoading && filteredOrders.length === 0" class="empty-state">
        <span class="material-symbols-rounded">inbox</span>
        <h3>No Orders Found</h3>
        <p>Try adjusting your filters or search criteria</p>
        <button class="action-button" @click="clearFilters">Clear Filters</button>
      </div>
    </div>

    <!-- Order Details Modal -->
    <Modal v-if="selectedOrder" @close="selectedOrder = null">
      <template #header>
        <h3>Order Details #{{ selectedOrder.id.slice(0,8) }}</h3>
      </template>
      <OrderDetails :order="selectedOrder" @update="refreshOrders" />
    </Modal>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { supabase } from '@/services/supabase'
import Modal from '@/components/Modal.vue'
import OrderDetails from './OrderDetails.vue'
import notifications from '@/store/notifications'

export default {
  name: 'OrderManager',
  components: { Modal, OrderDetails },

  setup() {
    const orders = ref([])
    const search = ref('')
    const statusFilter = ref('')
    const timeFilter = ref('all')
    const selectedOrder = ref(null)
    const isLoading = ref(false)

    const orderStatuses = [
      { value: 'pending', label: 'Pending' },
      { value: 'approved', label: 'Approved' },
      { value: 'rejected', label: 'Rejected' },
      { value: 'paid', label: 'Paid' },
      { value: 'preparing', label: 'Preparing' },
      { value: 'waiting_for_shipping', label: 'Ready to Ship' },
      { value: 'shipped', label: 'Shipped' },
      { value: 'delivered', label: 'Delivered' },
      { value: 'cancelled', label: 'Cancelled' }
    ]

    const filteredOrders = computed(() => {
      let filtered = orders.value

      // Search filter
      if (search.value) {
        const searchLower = search.value.toLowerCase()
        filtered = filtered.filter(order => 
          order.id.toLowerCase().includes(searchLower) ||
          order.customer_name.toLowerCase().includes(searchLower) ||
          order.customer_email.toLowerCase().includes(searchLower)
        )
      }

      // Status filter
      if (statusFilter.value) {
        filtered = filtered.filter(order => order.status === statusFilter.value)
      }

      // Time filter
      const now = new Date()
      switch (timeFilter.value) {
        case 'today':
          filtered = filtered.filter(order => 
            new Date(order.created_at).toDateString() === now.toDateString()
          )
          break
        case 'week':
          const weekAgo = new Date(now.setDate(now.getDate() - 7))
          filtered = filtered.filter(order => 
            new Date(order.created_at) >= weekAgo
          )
          break
        case 'month':
          const monthAgo = new Date(now.setMonth(now.getMonth() - 1))
          filtered = filtered.filter(order => 
            new Date(order.created_at) >= monthAgo
          )
          break
      }

      return filtered
    })

    // Calculate stats
    const pendingCount = computed(() => 
      orders.value.filter(o => o.status === 'pending').length
    )
    
    const processingCount = computed(() => 
      orders.value.filter(o => o.status === 'processing').length
    )
    
    const totalRevenue = computed(() => {
      return orders.value.reduce((sum, order) => {
        return sum + (order.total_amount || 0)
      }, 0).toFixed(2)
    })

    // Add computed properties for new status counts
    const approvedCount = computed(() => 
      orders.value.filter(o => o.status === 'approved').length
    )
    
    const preparingCount = computed(() => 
      orders.value.filter(o => o.status === 'preparing').length
    )

    // Fetch orders with customer info
    const fetchOrders = async () => {
      isLoading.value = true
      try {
        // Simple query first to verify it works
        const { data: ordersData, error: ordersError } = await supabase
          .from('orders')
          .select('*')
          .order('created_at', { ascending: false })

        if (ordersError) throw ordersError

        // Transform the data
        orders.value = ordersData.map(order => {
          const items = typeof order.items === 'string' ? JSON.parse(order.items) : order.items
          const shippingAddress = typeof order.shipping_address === 'string' ? 
            JSON.parse(order.shipping_address) : order.shipping_address

          return {
            ...order,
            items,
            shipping_address: shippingAddress,
            // Use shipping address info directly since it contains customer details
            customer_name: `${shippingAddress.first_name} ${shippingAddress.last_name}`,
            customer_email: shippingAddress.email
          }
        })

        notifications.success('Orders loaded successfully')
      } catch (error) {
        console.error('Error fetching orders:', error)
        notifications.error('Failed to load orders: ' + error.message)
      } finally {
        isLoading.value = false
      }
    }

    const updateOrderStatus = async (order) => {
      try {
        // Get current user first
        const { data: authData } = await supabase.auth.getUser()
        const adminId = authData && authData.user ? authData.user.id : null

        if (!adminId) {
          throw new Error('No admin user found')
        }

        // Then update the order
        const { error } = await supabase
          .from('orders')
          .update({
            status: order.status,
            admin_id: adminId,
            admin_action_at: new Date().toISOString()
          })
          .eq('id', order.id)

        if (error) throw error

        notifications.success('Order status updated')
        await fetchOrders()
      } catch (error) {
        console.error('Error updating order:', error)
        notifications.error('Failed to update order status')
      }
    }

    const viewOrderDetails = (order) => {
      selectedOrder.value = order
    }

    const formatPrice = (price) => {
      return Number(price).toFixed(2)
    }

    const formatDate = (date) => {
      return new Date(date).toLocaleString()
    }

    const clearFilters = () => {
      search.value = ''
      statusFilter.value = ''
      timeFilter.value = 'all'
    }

    onMounted(fetchOrders)

    return {
      orders,
      search,
      statusFilter,
      timeFilter,
      selectedOrder,
      orderStatuses,
      filteredOrders,
      pendingCount,
      processingCount,
      totalRevenue,
      updateOrderStatus,
      viewOrderDetails,
      formatPrice,
      formatDate,
      refreshOrders: fetchOrders,
      isLoading,
      approvedCount,
      preparingCount,
      clearFilters
    }
  }
}
</script>

<style scoped>
.order-manager {
  padding: 32px;
  background: var(--card-background);
  border-radius: 20px;
  border: 1px solid var(--card-border);
  display: flex;
  flex-direction: column;
  height: calc(100vh - 200px); /* Adjust based on your layout */
  min-height: 600px;
}

.panel-header {
  margin-bottom: 32px;
  flex: 0 0 auto;
}

.header-content {
  margin-bottom: 24px;
}

.header-content h2 {
  font-size: 24px;
  font-weight: 600;
  margin: 0;
  color: var(--text-primary);
}

.subtitle {
  color: var(--text-secondary);
  margin: 8px 0 0 0;
  font-size: 14px;
}

.filters {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 24px;
}

.search-box {
  background: var(--surface-background);
  border-radius: 12px;
  padding: 12px 16px;
  display: flex;
  align-items: center;
  gap: 12px;
  border: 1px solid var(--card-border);
  transition: all 0.2s ease;
}

.search-box:focus-within {
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 2px var(--accent-primary-alpha);
}

.search-box input {
  width: 100%;
  border: none;
  background: none;
  font-size: 14px;
  color: var(--text-primary);
}

.filter-group {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.filter-select {
  min-width: 180px;
  padding: 10px 16px;
  border-radius: 12px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.filter-select:hover {
  border-color: var(--accent-primary);
}

.filter-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  border-radius: 12px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
  color: var(--text-secondary);
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.filter-button:hover {
  background: var(--background-elevated);
  color: var(--text-primary);
}

.stat-pills {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 16px;
}

.stat-pill {
  background: var(--surface-background);
  border-radius: 16px;
  padding: 20px;
  display: flex;
  align-items: center;
  gap: 16px;
  border: 1px solid var(--card-border);
  transition: all 0.2s ease;
}

.stat-pill:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.stat-icon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--background-elevated);
}

.stat-content {
  flex: 1;
}

.stat-content span {
  display: block;
  font-size: 13px;
  color: var(--text-secondary);
  margin-bottom: 4px;
}

.stat-content strong {
  display: block;
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
}

.loading-overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  padding: 48px;
  color: var(--text-secondary);
  font-size: 16px;
}

.spin {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.empty-state {
  text-align: center;
  padding: 48px;
  color: var(--text-secondary);
}

.empty-state .material-symbols-rounded {
  font-size: 48px;
  margin-bottom: 16px;
}

.empty-state h3 {
  margin: 0 0 8px 0;
  font-size: 18px;
  color: var(--text-primary);
}

.empty-state p {
  margin: 0 0 24px 0;
  font-size: 14px;
}

.action-button {
  padding: 10px 20px;
  border-radius: 12px;
  border: none;
  background: var(--accent-primary);
  color: white;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.action-button:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.status-select {
  padding: 4px 8px;
  border-radius: 6px;
  border: none;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
}

.status-select.pending { background: var(--system-yellow); color: black; }
.status-select.processing { background: var(--system-blue); color: white; }
.status-select.shipped { background: var(--system-purple); color: white; }
.status-select.delivered { background: var(--system-green); color: white; }
.status-select.cancelled { background: var(--system-red); color: white; }
.status-select.approved { background: var(--system-green); color: white; }
.status-select.rejected { background: var(--system-red); color: white; }
.status-select.paid { background: var(--system-blue); color: white; }
.status-select.preparing { background: var(--system-orange); color: white; }
.status-select.waiting_for_shipping { background: var(--system-purple); color: white; }

.customer-info {
  display: flex;
  flex-direction: column;
}

.customer-info small {
  color: var(--text-secondary);
  font-size: 12px;
}

.price {
  font-family: monospace;
  font-size: 15px;
}

.table-container {
  margin: 0 -32px;  /* Negative margin to break out of padding */
  overflow-x: auto;
  border-top: 1px solid var(--card-border);
  flex: 1 1 auto;
  overflow-y: auto;
  margin-bottom: -32px; /* Compensate for parent padding */
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.data-table {
  width: 100%;
  min-width: 1000px;  /* Minimum width to prevent cramping */
  border-collapse: collapse;
}

.data-table th {
  position: sticky;
  top: 0;
  background: var(--card-background);
  padding: 16px 24px;
  text-align: left;
  font-size: 13px;
  font-weight: 600;
  color: var(--text-secondary);
  border-bottom: 1px solid var(--card-border);
  white-space: nowrap;
  z-index: 1;
}

.data-table td {
  padding: 16px 24px;
  font-size: 14px;
  border-bottom: 1px solid var(--divider-color);
  background: var(--card-background);
}

.data-table tr:hover td {
  background: var(--surface-background);
}

/* Column widths */
.data-table th:nth-child(1), 
.data-table td:nth-child(1) { width: 120px; }  /* Order ID */

.data-table th:nth-child(2),
.data-table td:nth-child(2) { width: 250px; }  /* Customer */

.data-table th:nth-child(3),
.data-table td:nth-child(3) { width: 100px; }  /* Items */

.data-table th:nth-child(4),
.data-table td:nth-child(4) { width: 100px; }  /* Total */

.data-table th:nth-child(5),
.data-table td:nth-child(5) { width: 150px; }  /* Status */

.data-table th:nth-child(6),
.data-table td:nth-child(6) { width: 150px; }  /* Date */

.data-table th:nth-child(7),
.data-table td:nth-child(7) { width: 100px; }  /* Actions */

@media (max-width: 768px) {
  .filters {
    flex-direction: column;
  }
  
  .stat-pills {
    flex-wrap: wrap;
  }
  
  .stat-pill {
    flex: 1;
    min-width: 140px;
  }
}
</style>
