export function getProxiedImageUrl(originalUrl) {
  // Check if we have a valid URL
  if (!originalUrl || typeof originalUrl !== 'string') {
    return '/placeholder.png'
  }

  if (originalUrl.startsWith('https://')) {
    // Convert the TCG API URL to our storage URL pattern
    const fileName = originalUrl.split('/').pop()
    const setCode = originalUrl.split('/').slice(-2)[0]
    
    // Return our own storage URL
    return `${process.env.VUE_APP_SUPABASE_URL}/storage/v1/object/public/card-images/${setCode}/${fileName}`
  }
  
  // Return placeholder for missing images
  return '/placeholder.png'
}

export function handleImageError(event) {
  console.warn('Image failed to load:', event.target.src)
  event.target.src = '/placeholder.png'
}
