<template>
  <div class="my-collection">
    <div class="collection-header">
      <h1>My Collection</h1>
      <div class="stats">
        <div class="stat-item">
          <span class="material-symbols-rounded">diamond</span>
          <div class="stat-content">
            <h3>Total Cards</h3>
            <p>{{ collection.length }}</p>
          </div>
        </div>
        <div class="stat-item">
          <span class="material-symbols-rounded">payments</span>
          <div class="stat-content">
            <h3>Total Value</h3>
            <p>${{ totalValue.toFixed(2) }}</p>
          </div>
        </div>
      </div>
    </div>

    <div class="cards-grid" v-if="collection.length">
      <div 
        v-for="card in collection" 
        :key="card.id"
        class="card-item"
        @click="showCardDetails(card)"
      >
        <div class="card-hover-overlay">
          <button 
            class="action-button"
            @click.stop="removeFromCollection(card.id)"
            title="Remove from Collection"
          >
            <span class="material-symbols-rounded">remove_circle</span>
          </button>
        </div>
        <div class="card-content">
          <img 
            :src="card.imageUrl || card.images.small" 
            :alt="card.name"
            loading="lazy"
          >
          <div class="card-details">
            <h3>{{ card.name }}</h3>
            <div class="card-meta">
              <span class="card-set">{{ card.set.name }}</span>
              <span v-if="card.rarity" class="card-rarity">{{ card.rarity }}</span>
            </div>
            <div class="card-price" v-if="card.price !== 'N/A'">
              €{{ Number(card.price).toFixed(2) }}
            </div>
          </div>
        </div>
      </div>
    </div>

    <div v-else class="empty-state">
      <span class="material-symbols-rounded">category</span>
      <h2>Your collection is empty</h2>
      <p>Start adding cards from the collection page!</p>
      <router-link to="/collection" class="browse-button">
        Browse Cards
      </router-link>
    </div>

    <CardDetailsModal
      v-if="selectedCard"
      :card="selectedCard"
      @close="selectedCard = null"
    />
  </div>
</template>

<script>
import Card from '@/components/Card.vue';
import CardDetailsModal from '@/components/CardDetailsModal.vue';
import collectionsStore from '@/store/collections';

export default {
  components: { Card, CardDetailsModal },
  data() {
    return {
      selectedCard: null,
      unsubscribe: null
    };
  },
  async created() {
    await collectionsStore.initializeUserData();
    this.unsubscribe = collectionsStore.subscribeToChanges();
  },
  unmounted() {
    if (this.unsubscribe) {
      this.unsubscribe();
    }
  },
  computed: {
    collection() {
      return collectionsStore.getCollection();
    },
    collectionStats() {
      return collectionsStore.getCollectionStats();
    },
    totalValue() {
      return this.collectionStats.totalValue;
    }
  },
  methods: {
    removeFromCollection(cardId) {
      collectionsStore.removeFromCollection(cardId);
    },
    showCardDetails(card) {
      this.selectedCard = card;
    }
  }
};
</script>

<style scoped>
.my-collection {
  padding: 24px;
}

.collection-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
}

.stats {
  display: flex;
  gap: 24px;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 24px;
  background: var(--card-background);
  border-radius: 12px;
  border: 1px solid var(--card-border);
}

.stat-content h3 {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0;
}

.stat-content p {
  font-size: 24px;
  font-weight: 600;
  color: var(--accent-primary);
  margin: 4px 0 0 0;
}

.empty-state {
  text-align: center;
  padding: 48px;
  color: var(--text-secondary);
}

.empty-state .material-symbols-rounded {
  font-size: 48px;
  margin-bottom: 16px;
}

.browse-button {
  display: inline-block;
  margin-top: 24px;
  padding: 12px 24px;
  background: var(--accent-primary);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.browse-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 24px;
  padding: 24px;
}

.card-item {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  background: var(--card-background);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.4s cubic-bezier(0.2, 0.8, 0.2, 1);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

.card-content {
  position: relative;
  z-index: 1;
}

.card-item:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
}

.card-hover-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  opacity: 0;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.card-item:hover .card-hover-overlay {
  opacity: 1;
}

.card-details {
  padding: 16px;
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.card-details h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  letter-spacing: -0.01em;
}

.card-meta {
  display: flex;
  gap: 8px;
  align-items: center;
  margin-top: 8px;
}

.card-set {
  font-size: 13px;
  color: var(--text-secondary);
  font-weight: 500;
}

.card-rarity {
  padding: 4px 10px;
  background: var(--accent-secondary);
  color: var(--accent-primary);
  border-radius: 100px;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: -0.01em;
}

.card-price {
  margin-top: 12px;
  font-size: 15px;
  font-weight: 600;
  color: var(--accent-primary);
}

.action-button {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  transition: all 0.3s cubic-bezier(0.2, 0.8, 0.2, 1);
}

.action-button:hover {
  transform: scale(1.1);
  background: var(--system-red);
}

@media (max-width: 768px) {
  .cards-grid {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    gap: 16px;
    padding: 16px;
  }
}
</style>
