<template>
  <div class="notifications-manager">
    <div class="panel-header">
      <h3>Send Notifications</h3>
      <button class="send-button" @click="sendNotification" :disabled="isSending">
        <span class="material-symbols-rounded">send</span>
        {{ isSending ? 'Sending...' : 'Send Notification' }}
      </button>
    </div>

    <div class="notification-form">
      <div class="form-group">
        <label>Recipients</label>
        <select v-model="recipients" class="select-input">
          <option value="all">All Users</option>
          <option value="specific">Specific Users</option>
        </select>
      </div>

      <div class="form-group" v-if="recipients === 'specific'">
        <label>Select Users</label>
        <div class="users-select">
          <div v-for="user in users" :key="user.id" class="user-checkbox">
            <input type="checkbox" 
                   :id="user.id" 
                   v-model="selectedUsers" 
                   :value="user.id">
            <label :for="user.id">{{ user.email }}</label>
          </div>
        </div>
      </div>

      <div class="form-group">
        <label>Type</label>
        <select v-model="type" class="select-input">
          <option value="info">Info</option>
          <option value="success">Success</option>
          <option value="warning">Warning</option>
          <option value="error">Error</option>
        </select>
      </div>

      <div class="form-group">
        <label>Message</label>
        <textarea 
          v-model="message" 
          placeholder="Enter notification message..."
          rows="4"
          class="text-input"
        ></textarea>
      </div>

      <div class="form-group">
        <label>Duration (seconds)</label>
        <input 
          type="number" 
          v-model.number="duration" 
          min="0" 
          step="1" 
          class="number-input"
        >
        <span class="hint">Set to 0 for permanent notification</span>
      </div>
    </div>

    <div v-if="error" class="error-message">
      {{ error }}
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { supabase } from '@/services/supabase'
import notifications from '@/store/notifications'

export default {
  name: 'NotificationsManager',
  setup() {
    const recipients = ref('all')
    const selectedUsers = ref([])
    const type = ref('info')
    const message = ref('')
    const duration = ref(5)
    const users = ref([])
    const error = ref('')
    const isSending = ref(false)

    const fetchUsers = async () => {
      const { data } = await supabase.from('profiles').select('id, email')
      users.value = data || []
    }

    const sendNotification = async () => {
      if (!message.value.trim()) {
        error.value = 'Please enter a message'
        return
      }

      isSending.value = true
      error.value = ''

      try {
        if (recipients.value === 'all') {
          // Send to all users
          await notifications.sendToAll({
            message: message.value,
            type: type.value,
            duration: duration.value * 1000
          })
        } else {
          // Send to specific users
          if (!selectedUsers.value.length) {
            error.value = 'Please select at least one user'
            return
          }

          await notifications.sendToUsers({
            userIds: selectedUsers.value,
            message: message.value,
            type: type.value,
            duration: duration.value * 1000
          })
        }

        // Clear form
        message.value = ''
        selectedUsers.value = []
        notifications.success('Notification sent successfully')
      } catch (err) {
        error.value = err.message
      } finally {
        isSending.value = false
      }
    }

    onMounted(fetchUsers)

    return {
      recipients,
      selectedUsers,
      type,
      message,
      duration,
      users,
      error,
      isSending,
      sendNotification
    }
  }
}
</script>

<style scoped>
.notifications-manager {
  padding: 24px;
}

.notification-form {
  display: flex;
  flex-direction: column;
  gap: 24px;
  max-width: 600px;
  margin-top: 24px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-size: 14px;
  font-weight: 500;
  color: var(--text-secondary);
}

.select-input,
.text-input,
.number-input {
  padding: 12px;
  border-radius: 8px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 14px;
}

.text-input {
  resize: vertical;
  min-height: 100px;
}

.users-select {
  max-height: 200px;
  overflow-y: auto;
  padding: 12px;
  border-radius: 8px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
}

.user-checkbox {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px;
}

.hint {
  font-size: 12px;
  color: var(--text-tertiary);
}

.error-message {
  margin-top: 16px;
  padding: 12px;
  border-radius: 8px;
  background: var(--system-red);
  color: white;
  font-size: 14px;
}

.send-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  border-radius: 8px;
  border: none;
  background: var(--accent-primary);
  color: white;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.send-button:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.send-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}
</style>
