import { reactive, watch } from 'vue'
import pokemonApi from '@/services/pokemonApi'

const savedCart = localStorage.getItem('cart')
const initialState = savedCart ? JSON.parse(savedCart) : {
  items: [],
  total: 0,
  show: false
}

const state = reactive(initialState)

// Save cart to localStorage whenever it changes
watch(() => state.items, (items) => {
  localStorage.setItem('cart', JSON.stringify({
    items,
    total: state.total,
    show: state.show
  }))
}, { deep: true })

export default {
  state,

  toggleCart() {
    state.show = !state.show
  },

  addItem(product) {
    const existingItem = this.state.items.find(i => i.id === product.id)
    if (existingItem) {
      existingItem.quantity += 1
    } else {
      // Safely extract tcg_id from product
      const tcgId = product.tcgCard && product.tcgCard.id 
        ? product.tcgCard.id 
        : (product.tcg_id || null);

      this.state.items.push({
        id: product.id,
        name: product.name,
        price: parseFloat(product.price) || 0,
        image_url: product.image_url,
        type: product.type || 'single',
        tcg_id: tcgId,
        condition: product.condition,
        metadata: product.metadata || {},
        quantity: 1 // Make sure quantity is always initialized to 1
      })
    }
    this.calculateTotal()
    this.saveCart()
  },

  removeItem(itemId) {
    state.items = state.items.filter(item => item.id !== itemId)
    this.calculateTotal()
  },

  updateQuantity(itemId, quantity) {
    const item = state.items.find(i => i.id === itemId)
    if (item) {
      item.quantity = quantity
      this.calculateTotal()
    }
  },

  calculateTotal() {
    state.total = state.items.reduce((sum, item) => {
      const price = parseFloat(item.price) || 0
      const quantity = parseInt(item.quantity) || 0
      return sum + (price * quantity)
    }, 0)
  },

  clear() {
    state.items = []
    state.total = 0
    state.show = false
    localStorage.removeItem('cart')
  },

  saveCart() {
    localStorage.setItem('cart', JSON.stringify({
      items: state.items,
      total: state.total,
      show: state.show
    }))
  }
}
