import { ref } from 'vue'
import { supabase } from '@/services/supabase'

export function useStoreAvailability() {
  const availableProducts = ref(new Map())

  const checkStoreAvailability = async (cardId) => {
    try {
      const { data } = await supabase
        .from('products')
        .select('*')
        .eq('card_id', cardId)
        .eq('active', true)
        .gt('stock', 0)
        .single()
      
      availableProducts.value.set(cardId, data || null)
      return data
    } catch (error) {
      console.error('Error checking store availability:', error)
      return null
    }
  }

  return {
    availableProducts,
    checkStoreAvailability
  }
}
