<template>
  <div class="notification-center">
    <TransitionGroup 
      name="notification" 
      tag="div" 
      class="notifications-container"
    >
      <div
        v-for="notification in notifications"
        :key="notification.id"
        :class="['notification', notification.type, { 'unread': !notification.read }]"
        @click="markAsRead(notification.id)"
      >
        <span class="icon material-symbols-rounded">
          {{ getIcon(notification.type) }}
        </span>
        <div class="content">
          <p>{{ notification.message }}</p>
          <span class="timestamp">{{ formatTime(notification.timestamp) }}</span>
        </div>
        <button class="close-button" @click.stop="dismiss(notification.id)">
          <span class="material-symbols-rounded">close</span>
        </button>
      </div>
    </TransitionGroup>
  </div>
</template>

<script>
import { computed, onMounted } from 'vue'
import notificationsStore from '@/store/notifications'

export default {
  name: 'NotificationCenter',
  setup() {
    // Make sure we use the store's state property
    const notifications = computed(() => notificationsStore.state.notifications)

    const getIcon = (type) => {
      const icons = {
        success: 'check_circle',
        error: 'error',
        warning: 'warning',
        info: 'info'
      }
      return icons[type] || 'notifications'
    }

    const formatTime = (timestamp) => {
      return new Date(timestamp).toLocaleTimeString()
    }

    const dismiss = (id) => {
      notificationsStore.remove(id)
    }

    const markAsRead = async (id) => {
      await notificationsStore.markAsRead(id)
    }

    onMounted(() => {
      notificationsStore.loadUserNotifications()
    })

    return {
      notifications,
      getIcon,
      formatTime,
      dismiss,
      markAsRead
    }
  }
}
</script>

<style scoped>
.notification-center {
  position: fixed;
  top: 24px;
  right: 24px;
  z-index: var(--z-index-notifications);
  pointer-events: none;
  max-width: 100%;
  width: 400px;
}

.notifications-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.notification {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 16px;
  background: var(--card-background);
  border-radius: 12px;
  backdrop-filter: var(--blur-md);
  -webkit-backdrop-filter: var(--blur-md);
  border: 1px solid var(--card-border);
  pointer-events: auto;
  cursor: pointer;
  animation: slideIn 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

.notification.unread {
  border-left: 3px solid var(--accent-primary);
  background: var(--card-background-elevated);
}

.notification.unread::before {
  content: '';
  position: absolute;
  top: 12px;
  right: 12px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--accent-primary);
}

.notification .content {
  flex: 1;
}

.notification p {
  margin: 0;
  font-size: 14px;
  line-height: 1.4;
}

.notification .timestamp {
  display: block;
  font-size: 12px;
  color: var(--text-tertiary);
  margin-top: 4px;
}

.notification .icon {
  color: var(--text-secondary);
}

.notification.success .icon {
  color: var(--system-green);
}

.notification.error .icon {
  color: var(--system-red);
}

.notification.warning .icon {
  color: var(--system-orange);
}

.notification.info .icon {
  color: var(--system-blue);
}

.close-button {
  background: none;
  border: none;
  color: var(--text-tertiary);
  padding: 4px;
  cursor: pointer;
  border-radius: 50%;
  transition: all 0.2s ease;
}

.close-button:hover {
  background: var(--surface-background);
  color: var(--text-primary);
}

/* Transitions */
.notification-enter-active,
.notification-leave-active {
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

.notification-enter-from {
  opacity: 0;
  transform: translateX(100%);
}

.notification-leave-to {
  opacity: 0;
  transform: translateX(100%);
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (max-width: 768px) {
  .notification-center {
    top: auto;
    bottom: 24px;
    left: 24px;
    right: 24px;
    width: auto;
  }
}
</style>
