import axios from 'axios';

const API_KEY = process.env.VUE_APP_POKEMON_TCG_API_KEY || 'your-api-key-here';
const BASE_URL = 'https://api.pokemontcg.io/v2';

const apiClient = axios.create({
  baseURL: BASE_URL,
  headers: {
    'X-Api-Key': API_KEY,
    'Content-Type': 'application/json',
  }
});

export default {
  async getCards(params = {}) {
    const response = await apiClient.get('/cards', {
      params: {
        pageSize: 250,
        page: params.page || 1,
        q: params.query || '',
        orderBy: params.orderBy || 'name',
        select: 'id,name,images,types,rarity,cardmarket,set',
        ...params
      }
    });
    return response.data;
  },

  async getSets() {
    const response = await apiClient.get('/sets', {
      params: {
        orderBy: '-releaseDate',
        select: 'id,name,images,releaseDate,series'
      }
    });
    return response.data;
  },

  async getCardById(id) {
    const response = await apiClient.get(`/cards/${id}`);
    return response.data;
  },

  async getCardsByQuery(query) {
    const response = await apiClient.get('/cards', {
      params: { q: query }
    });
    return response.data;
  },

  // Add method to fetch single card by ID
  getCard(id) {
    return apiClient.get(`/cards/${id}`);
  }
};
