<template>
  <div class="modal-overlay" @click="$emit('close')">
    <div class="modal-container" :style="{ maxWidth: `${maxWidth}px` }" @click.stop>
      <div class="modal-header">
        <slot name="header"></slot>
        <button class="close-button" @click="$emit('close')">
          <span class="material-symbols-rounded">close</span>
        </button>
      </div>
      <div class="modal-content">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Modal',
  props: {
    maxWidth: {
      type: Number,
      default: 600
    }
  },
  emits: ['close']
}
</script>

<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: var(--z-index-modal);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  padding: 24px;
}

.modal-container {
  background: var(--background-secondary);
  border-radius: 20px;
  width: 95%;  /* Increased from 90% */
  max-height: 95vh; /* Increased from 90vh */
  overflow-y: auto;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--card-border);
  animation: modalIn 0.3s cubic-bezier(0.2, 0.8, 0.2, 1);
}

.modal-header {
  position: relative;
  padding: 24px 32px; /* Increased horizontal padding */
  border-bottom: 1px solid var(--card-border);
}

.close-button {
  position: absolute;
  top: 24px;
  right: 24px;
  background: var(--surface-background);
  border: none;
  color: var(--text-secondary);
  width: 32px;
  height: 32px;
  border-radius: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.close-button:hover {
  background: var(--background-elevated);
  color: var(--text-primary);
  transform: scale(1.1);
}

.modal-content {
  padding: 0; /* Remove default padding to let child components control their padding */
  overflow-y: auto;
}

@keyframes modalIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>
