<template>
  <Modal :maxWidth="1200" @close="close">
    <template #header>
      <h2>{{ product.name }}</h2>
    </template>
    <div class="quick-preview-content">
      <div class="preview-image">
        <img :src="product.image_url" :alt="product.name" />
      </div>
      <div class="preview-details">
        <div class="details-content">
          <div class="meta">
            <span v-if="product.metadata && product.metadata.set" class="set-badge">
              {{ product.metadata.set.name }}
            </span>
            <span v-if="product.metadata && product.metadata.rarity" class="badge">
              {{ product.metadata.rarity }}
            </span>
          </div>
          
          <div class="pricing-info">
            <div class="price-box">
              <span class="label">Market Price</span>
              <span class="value">${{ product.marketPrice || product.price }}</span>
            </div>
            <div class="price-box">
              <span class="label">Our Price</span>
              <span class="value highlight">${{ product.price }}</span>
            </div>
          </div>

          <div class="stock-info">
            <span class="material-symbols-rounded">
              {{ product.stock > 0 ? 'inventory_2' : 'inventory_2_off' }}
            </span>
            <span>{{ product.stock > 0 ? `${product.stock} in stock` : 'Out of stock' }}</span>
          </div>

          <p v-if="product.description" class="description">
            {{ product.description }}
          </p>
        </div>
        
        <div class="action-buttons">
          <button class="add-to-cart" @click="addToCart">
            Add to Cart
          </button>
        </div>
      </div>
    </div>
  </Modal>
</template>

<script>
import { defineComponent } from 'vue'
import cart from '@/store/cart'
import Modal from '@/components/Modal.vue'

export default defineComponent({
  name: 'QuickPreview',
  components: { Modal },
  props: {
    product: Object
  },
  emits: ['close'],
  setup(props, { emit }) {
    function close() {
      emit('close')
    }
    function addToCart() {
      cart.addItem(props.product)
      close()
    }
    return {
      close,
      addToCart
    }
  }
})
</script>

<style scoped>
.quick-preview-content {
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  gap: 32px;
  padding: 24px 32px 32px;
  max-height: calc(90vh - 120px); /* Account for header and padding */
}

.preview-image {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.preview-image img {
  max-height: 600px;
  width: auto;
  object-fit: contain;
  border-radius: 16px;
  box-shadow: var(--shadow-md);
}

.preview-details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 24px;
}

.details-content {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.meta {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.set-badge, .badge {
  padding: 8px 16px;
  border-radius: 100px;
  font-size: 14px;
  background: var(--surface-background);
  color: var(--text-secondary);
}

.pricing-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

.price-box {
  padding: 16px;
  background: var(--surface-background);
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.price-box .label {
  font-size: 14px;
  color: var(--text-secondary);
}

.price-box .value {
  font-size: 24px;
  font-weight: 600;
}

.price-box .value.highlight {
  color: var(--accent-primary);
}

.stock-info {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background: var(--surface-background);
  border-radius: 12px;
  color: var(--text-secondary);
}

.description {
  font-size: 15px;
  line-height: 1.6;
  color: var(--text-secondary);
  margin: 0;
}

.action-buttons {
  display: flex;
  gap: 16px;
  margin-top: auto;
}

.add-to-cart {
  flex: 1;
  padding: 16px 24px;
  border: none;
  border-radius: 12px;
  background: var(--accent-primary);
  color: white;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.add-to-cart:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

@media (max-width: 768px) {
  .quick-preview-content {
    grid-template-columns: 1fr;
    gap: 24px;
    padding: 16px;
  }

  .preview-image img {
    max-height: 400px;
  }

  .pricing-info {
    grid-template-columns: 1fr;
  }
}
</style>
