<template>
  <div class="order-details">
    <!-- Customer Information -->
    <section class="detail-section">
      <h4>Customer Information</h4>
      <div class="info-grid">
        <div class="info-item">
          <span class="label">Name</span>
          <span class="value">{{ order.customer_name }}</span>
        </div>
        <div class="info-item">
          <span class="label">Email</span>
          <span class="value">{{ order.customer_email }}</span>
        </div>
      </div>
    </section>

    <!-- Shipping Address -->
    <section class="detail-section">
      <h4>Shipping Address</h4>
      <div class="address-box">
        <p>{{ order.shipping_address.first_name }} {{ order.shipping_address.last_name }}</p>
        <p>{{ order.shipping_address.address }}</p>
        <p>{{ order.shipping_address.postal_code }} {{ order.shipping_address.city }}</p>
        <p>{{ order.shipping_address.country }}</p>
      </div>
    </section>

    <!-- Order Items -->
    <section class="detail-section">
      <h4>Order Items</h4>
      <div class="items-list">
        <div v-for="item in order.items" :key="item.id" class="order-item">
          <div class="item-image">
            <img v-if="item.image_url" :src="item.image_url" :alt="item.name">
            <div v-else class="image-placeholder">
              <span class="material-symbols-rounded">image</span>
            </div>
          </div>
          <div class="item-info">
            <h5>{{ item.name }}</h5>
            <div class="item-meta">
              <span class="quantity">{{ item.quantity }}x</span>
              <span class="price">€{{ formatPrice(item.price) }}</span>
            </div>
          </div>
          <div class="item-total">
            €{{ formatPrice(item.price * item.quantity) }}
          </div>
        </div>
      </div>
    </section>

    <!-- Order Summary -->
    <section class="detail-section">
      <h4>Order Summary</h4>
      <div class="summary-box">
        <div class="summary-row">
          <span>Subtotal</span>
          <span>€{{ formatPrice(order.total_amount) }}</span>
        </div>
        <div class="summary-row">
          <span>Shipping</span>
          <span>Free</span>
        </div>
        <div class="summary-row total">
          <span>Total</span>
          <span>€{{ formatPrice(order.total_amount) }}</span>
        </div>
      </div>
    </section>

    <!-- Status History -->
    <section class="detail-section">
      <h4>Status History</h4>
      <div class="status-timeline">
        <div v-for="(status, index) in order.status_history" 
             :key="index"
             class="timeline-item">
          <div class="timeline-dot" :class="status.status"></div>
          <div class="timeline-content">
            <span class="status">{{ status.status }}</span>
            <span class="date">{{ formatDate(status.timestamp) }}</span>
          </div>
        </div>
      </div>
    </section>

    <!-- Actions -->
    <div class="actions-bar">
      <select v-model="newStatus" class="status-select">
        <option value="">Update Status</option>
        <option value="processing">Mark as Processing</option>
        <option value="shipped">Mark as Shipped</option>
        <option value="delivered">Mark as Delivered</option>
        <option value="cancelled">Cancel Order</option>
      </select>
      <button @click="updateStatus" 
              class="action-button"
              :disabled="!newStatus">
        Update Status
      </button>
      <button @click="downloadInvoice" class="action-button secondary">
        <span class="material-symbols-rounded">receipt_long</span>
        Download Invoice
      </button>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import { supabase } from '@/services/supabase'
import notifications from '@/store/notifications'

export default {
  name: 'OrderDetails',
  props: {
    order: {
      type: Object,
      required: true
    }
  },
  emits: ['update'],
  
  setup(props, { emit }) {
    const newStatus = ref('')

    const formatPrice = (price) => {
      return Number(price).toFixed(2)
    }

    const formatDate = (date) => {
      return new Date(date).toLocaleString()
    }

    const updateStatus = async () => {
      if (!newStatus.value) return

      try {
        const { error } = await supabase
          .from('orders')
          .update({ 
            status: newStatus.value,
            status_history: [
              ...(props.order.status_history || []),
              { status: newStatus.value, timestamp: new Date() }
            ]
          })
          .eq('id', props.order.id)

        if (error) throw error

        notifications.success('Order status updated')
        emit('update')
      } catch (error) {
        console.error('Error updating order:', error)
        notifications.error('Failed to update order status')
      }
    }

    const downloadInvoice = () => {
      // Implement invoice download logic
      console.log('Downloading invoice for order:', props.order.id)
    }

    return {
      newStatus,
      formatPrice,
      formatDate,
      updateStatus,
      downloadInvoice
    }
  }
}
</script>

<style scoped>
.order-details {
  padding: 24px;
  color: var(--text-primary);
}

.detail-section {
  margin-bottom: 32px;
}

.detail-section h4 {
  font-size: 16px;
  font-weight: 600;
  margin: 0 0 16px 0;
  color: var(--text-secondary);
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
}

.info-item {
  background: var(--surface-background);
  padding: 12px 16px;
  border-radius: 8px;
}

.info-item .label {
  display: block;
  font-size: 13px;
  color: var(--text-secondary);
  margin-bottom: 4px;
}

.info-item .value {
  font-size: 15px;
  font-weight: 500;
}

.address-box {
  background: var(--surface-background);
  padding: 16px;
  border-radius: 12px;
}

.address-box p {
  margin: 4px 0;
  font-size: 14px;
}

.items-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.order-item {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 12px;
  background: var(--surface-background);
  border-radius: 12px;
}

.item-image {
  width: 48px;
  height: 48px;
  border-radius: 8px;
  overflow: hidden;
  background: var(--background-elevated);
}

.item-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.image-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-tertiary);
}

.item-info {
  flex: 1;
}

.item-info h5 {
  margin: 0;
  font-size: 14px;
}

.item-meta {
  margin-top: 4px;
  font-size: 13px;
  color: var(--text-secondary);
}

.item-total {
  font-family: monospace;
  font-size: 15px;
  font-weight: 600;
  color: var(--accent-primary);
}

.summary-box {
  background: var(--surface-background);
  padding: 16px;
  border-radius: 12px;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  padding: 8px 0;
  font-size: 14px;
}

.summary-row.total {
  margin-top: 8px;
  padding-top: 16px;
  border-top: 1px solid var(--divider-color);
  font-weight: 600;
  font-size: 16px;
}

.status-timeline {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.timeline-item {
  display: flex;
  align-items: center;
  gap: 12px;
}

.timeline-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: var(--system-blue);
}

.timeline-dot.processing { background: var(--system-blue); }
.timeline-dot.shipped { background: var(--system-purple); }
.timeline-dot.delivered { background: var(--system-green); }
.timeline-dot.cancelled { background: var(--system-red); }

.timeline-content {
  display: flex;
  flex-direction: column;
}

.timeline-content .status {
  font-weight: 500;
  font-size: 14px;
}

.timeline-content .date {
  font-size: 12px;
  color: var(--text-secondary);
}

.actions-bar {
  position: sticky;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 16px;
  background: var(--card-background);
  border-top: 1px solid var(--divider-color);
  display: flex;
  gap: 12px;
  margin: 0 -24px -24px -24px;
}

.status-select {
  flex: 1;
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 14px;
}

.action-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.action-button:not(.secondary) {
  background: var(--accent-primary);
  color: white;
}

.action-button.secondary {
  background: var(--surface-background);
  color: var(--text-primary);
}

.action-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .actions-bar {
    flex-direction: column;
  }
  
  .status-select {
    width: 100%;
  }
  
  .action-button {
    width: 100%;
    justify-content: center;
  }
}
</style>
