<template>
  <div class="cards-manager">
    <div class="panel-header">
      <div class="actions">
        <button class="action-button" @click="syncCards">
          <span class="material-symbols-rounded">sync</span>
          Sync with API
        </button>
        <button class="action-button" @click="importCards">
          <span class="material-symbols-rounded">upload_file</span>
          Import Cards
        </button>
      </div>
    </div>

    <div class="panel-content">
      <div class="cards-stats">
        <div class="stat-item">
          <h4>Total Cards</h4>
          <p>{{ stats.total }}</p>
        </div>
        <div class="stat-item">
          <h4>Last Synced</h4>
          <p>{{ stats.lastSync ? formatDate(stats.lastSync) : 'Never' }}</p>
        </div>
      </div>

      <div class="cards-grid">
        <div v-for="card in cards" :key="card.id" class="card-item">
          <img :src="card.imageUrl" :alt="card.name">
          <div class="card-info">
            <h3>{{ card.name }}</h3>
            <p>${{ card.price }}</p>
            <div class="card-actions">
              <button @click="editCard(card)">
                <span class="material-symbols-rounded">edit</span>
              </button>
              <button @click="deleteCard(card.id)" class="danger">
                <span class="material-symbols-rounded">delete</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { supabase } from '@/services/supabase'

export default {
  name: 'CardsManager',
  setup() {
    const cards = ref([])
    const stats = ref({
      total: 0,
      lastSync: null
    })

    async function fetchCards() {
      const { data, error } = await supabase
        .from('cards')
        .select('*')
        .order('name')

      if (!error) {
        cards.value = data
        stats.value.total = data.length
      }
    }

    // Add other card management methods here

    onMounted(fetchCards)

    return {
      cards,
      stats,
      formatDate: (date) => new Date(date).toLocaleDateString()
    }
  }
}
</script>

<style scoped>
.cards-manager {
  padding: 24px;
}

/* Add more styles here */
</style>
