import { reactive } from 'vue';
import { supabase } from '@/services/supabase';
import auth from '@/store/auth';

const state = reactive({
  // Browse state
  cards: [],
  loading: false,
  error: null,
  filters: {
    page: 1,
    query: '',
    orderBy: 'name'
  },
  totalCards: 0,

  // Personal collection state
  myCollection: [],
  wishlist: []
});

export default {
  state,

  // Browse methods
  async loadCards(params = {}) {
    state.loading = true;
    state.error = null;
    try {
      const response = await pokemonApi.getCards({
        ...state.filters,
        ...params
      });
      state.cards = response.data;
      state.totalCards = response.totalCount;
      state.filters = { ...state.filters, ...params };
    } catch (error) {
      state.error = error.message;
    } finally {
      state.loading = false;
    }
  },

  setFilter(filterName, value) {
    state.filters[filterName] = value;
  },

  // Collection methods
  async initializeUserData() {
    if (!auth.state.user) return;
    
    try {
      state.loading = true;
      // Load collection
      const { data: collection } = await supabase
        .from('collections')
        .select('*')
        .eq('user_id', auth.state.user.id);
      
      state.myCollection = collection ? collection.map(item => item.card_data) : [];

      // Load wishlist
      const { data: wishlist } = await supabase
        .from('wishlists')
        .select('*')
        .eq('user_id', auth.state.user.id);
      
      state.wishlist = wishlist ? wishlist.map(item => item.card_data) : [];
    } catch (error) {
      state.error = error.message;
    } finally {
      state.loading = false;
    }
  },

  async addToCollection(card) {
    if (!auth.state.user || this.isInCollection(card.id)) return;

    try {
      const cardData = {
        ...card,
        price: card.cardmarket && card.cardmarket.prices && card.cardmarket.prices.averageSellPrice || 'N/A',
        added_at: new Date().toISOString()
      };

      const { error } = await supabase
        .from('collections')
        .insert({
          user_id: auth.state.user.id,
          card_id: card.id,
          card_data: cardData
        });

      if (error) throw error;
      state.myCollection.push(cardData);
    } catch (error) {
      state.error = error.message;
    }
  },

  async removeFromCollection(cardId) {
    if (!auth.state.user) return;

    try {
      const { error } = await supabase
        .from('collections')
        .delete()
        .eq('user_id', auth.state.user.id)
        .eq('card_id', cardId);

      if (error) throw error;
      state.myCollection = state.myCollection.filter(card => card.id !== cardId);
    } catch (error) {
      state.error = error.message;
    }
  },

  async addToWishlist(card) {
    if (!auth.state.user || this.isInWishlist(card.id)) return;

    try {
      const { error } = await supabase
        .from('wishlists')
        .insert({
          user_id: auth.state.user.id,
          card_id: card.id,
          card_data: card
        });

      if (error) throw error;
      state.wishlist.push(card);
    } catch (error) {
      state.error = error.message;
    }
  },

  async removeFromWishlist(cardId) {
    if (!auth.state.user) return;

    try {
      const { error } = await supabase
        .from('wishlists')
        .delete()
        .eq('user_id', auth.state.user.id)
        .eq('card_id', cardId);

      if (error) throw error;
      state.wishlist = state.wishlist.filter(card => card.id !== cardId);
    } catch (error) {
      state.error = error.message;
    }
  },

  isInCollection(cardId) {
    return state.myCollection.some(card => card.id === cardId);
  },

  isInWishlist(cardId) {
    return state.wishlist.some(card => card.id === cardId);
  },

  getCollection() {
    return state.myCollection;
  },

  getWishlist() {
    return state.wishlist;
  },

  subscribeToChanges() {
    if (!auth.state.user) return;

    // Subscribe to collection changes
    const collectionSubscription = supabase
      .channel('collection_changes')
      .on('postgres_changes', {
        event: '*',
        schema: 'public',
        table: 'collections',
        filter: `user_id=eq.${auth.state.user.id}`
      }, (payload) => {
        if (payload.eventType === 'INSERT') {
          state.myCollection.push(payload.new.card_data);
        } else if (payload.eventType === 'DELETE') {
          state.myCollection = state.myCollection.filter(card => card.id !== payload.old.card_id);
        }
      })
      .subscribe();

    // Subscribe to wishlist changes
    const wishlistSubscription = supabase
      .channel('wishlist_changes')
      .on('postgres_changes', {
        event: '*',
        schema: 'public',
        table: 'wishlists',
        filter: `user_id=eq.${auth.state.user.id}`
      }, (payload) => {
        if (payload.eventType === 'INSERT') {
          state.wishlist.push(payload.new.card_data);
        } else if (payload.eventType === 'DELETE') {
          state.wishlist = state.wishlist.filter(card => card.id !== payload.old.card_id);
        }
      })
      .subscribe();

    return () => {
      collectionSubscription.unsubscribe();
      wishlistSubscription.unsubscribe();
    };
  },

  // Collection statistics
  getCollectionStats() {
    const totalCards = state.myCollection.length;
    const totalValue = state.myCollection.reduce((sum, card) => {
      const price = typeof card.price === 'number' ? card.price : 0;
      return sum + price;
    }, 0);

    return {
      totalCards,
      totalValue
    };
  }
};
