import axios from 'axios';

const API_KEY = process.env.VUE_APP_POKEMON_TCG_API_KEY;
const BASE_URL = 'https://api.pokemontcg.io/v2';

const apiClient = axios.create({
  baseURL: BASE_URL,
  headers: {
    'X-Api-Key': API_KEY
  }
});

export const pokemonTcgApi = {
  async searchCards({ q, page = 1, pageSize = 20 }) {
    try {
      const response = await apiClient.get('/cards', {
        params: {
          q: `name:${q}*`,
          page,
          pageSize,
          orderBy: 'name',
          select: 'id,name,images,types,rarity,cardmarket,set,flavorText'
        }
      });

      if (!response.data || !response.data.data) {
        throw new Error('Invalid API response format');
      }

      return {
        data: response.data.data,
        hasMore: response.data.totalCount > page * pageSize,
        total: response.data.totalCount
      };
    } catch (error) {
      if (error.response && error.response.status === 401) {
        throw new Error('Invalid API key. Please check your configuration.');
      }
      if (error.response && error.response.status === 429) {
        throw new Error('Rate limit exceeded. Please try again later.');
      }
      console.error('Pokemon TCG API Error:', error);
      throw new Error('Failed to fetch cards. Please try again.');
    }
  }
};
