<template>
  <div class="settings-manager">
    <div class="settings-grid">
      <div class="settings-card">
        <h3>General Settings</h3>
        <div class="settings-section">
          <div class="setting-item">
            <div class="setting-content">
              <h4>Maintenance Mode</h4>
              <p>Block access to the application</p>
            </div>
            <label class="switch">
              <input type="checkbox" v-model="settings.maintenanceMode">
              <span class="slider"></span>
            </label>
          </div>
          
          <div class="setting-item">
            <div class="setting-content">
              <h4>User Registration</h4>
              <p>Allow new users to register</p>
            </div>
            <label class="switch">
              <input type="checkbox" v-model="settings.allowRegistration">
              <span class="slider"></span>
            </label>
          </div>

          <div class="setting-item">
            <div class="setting-content">
              <h4>Email Notifications</h4>
              <p>Send emails for important updates</p>
            </div>
            <label class="switch">
              <input type="checkbox" v-model="settings.emailNotifications">
              <span class="slider"></span>
            </label>
          </div>
        </div>
      </div>

      <div class="settings-card">
        <h3>API Configuration</h3>
        <div class="settings-section">
          <div class="setting-field">
            <label>API Key</label>
            <div class="input-group">
              <input 
                :type="showApiKey ? 'text' : 'password'" 
                v-model="settings.apiKey"
              >
              <button @click="showApiKey = !showApiKey" class="icon-button">
                <span class="material-symbols-rounded">
                  {{ showApiKey ? 'visibility_off' : 'visibility' }}
                </span>
              </button>
            </div>
          </div>

          <div class="setting-field">
            <label>Rate Limit (requests/minute)</label>
            <input 
              type="number" 
              v-model="settings.rateLimit"
              min="1" 
              max="1000"
            >
          </div>

          <div class="setting-field">
            <label>API Version</label>
            <select v-model="settings.apiVersion">
              <option value="v1">Version 1.0</option>
              <option value="v2">Version 2.0</option>
            </select>
          </div>
        </div>
      </div>

      <div class="settings-card">
        <h3>Cache Settings</h3>
        <div class="settings-section">
          <div class="setting-field">
            <label>Cache Duration (minutes)</label>
            <input 
              type="number" 
              v-model="settings.cacheDuration"
              min="5" 
              max="1440"
            >
          </div>

          <button @click="clearCache" class="action-button">
            <span class="material-symbols-rounded">delete</span>
            Clear Cache
          </button>
        </div>
      </div>
    </div>

    <div class="settings-actions">
      <button class="secondary" @click="resetSettings">Reset to Defaults</button>
      <button class="primary" @click="saveSettings" :disabled="isSaving">
        {{ isSaving ? 'Saving...' : 'Save Changes' }}
      </button>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { supabase } from '@/services/supabase'

export default {
  name: 'SettingsManager',
  setup() {
    const settings = ref({
      maintenanceMode: false,
      allowRegistration: true,
      emailNotifications: false,
      apiKey: '',
      rateLimit: 100,
      apiVersion: 'v1',
      cacheDuration: 60
    })

    const showApiKey = ref(false)
    const isSaving = ref(false)

    async function loadSettings() {
      const { data } = await supabase
        .from('app_settings')
        .select('*')
        .single()

      if (data) {
        settings.value = { ...settings.value, ...data }
      }
    }

    async function saveSettings() {
      isSaving.value = true
      await supabase
        .from('app_settings')
        .upsert(settings.value)
      isSaving.value = false
    }

    async function clearCache() {
      // Add logic to clear cache
    }

    function resetSettings() {
      settings.value = {
        maintenanceMode: false,
        allowRegistration: true,
        emailNotifications: false,
        apiKey: '',
        rateLimit: 100,
        apiVersion: 'v1',
        cacheDuration: 60
      }
    }

    onMounted(loadSettings)

    return {
      settings,
      showApiKey,
      isSaving,
      saveSettings,
      clearCache,
      resetSettings
    }
  }
}
</script>

<style scoped>
.settings-manager {
  padding: 24px;
}

.settings-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  margin-bottom: 24px;
}

.settings-card {
  background: var(--surface-background);
  border-radius: 16px;
  border: 1px solid var(--card-border);
  overflow: hidden;
}

.settings-card h3 {
  font-size: 18px;
  font-weight: 600;
  margin: 0;
  padding: 20px 24px;
  border-bottom: 1px solid var(--card-border);
}

.settings-section {
  padding: 16px;
}

.setting-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  background: var(--card-background);
  border-radius: 12px;
  margin-bottom: 12px;
}

.setting-content h4 {
  margin: 0;
  font-size: 15px;
  font-weight: 500;
}

.setting-content p {
  margin: 4px 0 0 0;
  font-size: 13px;
  color: var(--text-secondary);
}

.setting-field {
  margin-bottom: 20px;
}

.setting-field label {
  display: block;
  font-size: 13px;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.setting-field input,
.setting-field select {
  width: 100%;
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid var(--card-border);
  background: var(--card-background);
  color: var(--text-primary);
  font-size: 15px;
  transition: all 0.2s ease;
}

.setting-field input:focus,
.setting-field select:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 2px var(--accent-primary-alpha);
}

.input-group {
  display: flex;
  gap: 8px;
}

.input-group input {
  flex: 1;
}

.switch {
  position: relative;
  display: inline-block;
  width: 44px;
  height: 24px;
}

/* ... rest of the styles from your existing SettingsManager ... */
</style>
