<template>
  <div class="modal-overlay" @click="$emit('close')">
    <div class="modal-content" @click.stop>
      <button class="close-button" @click="$emit('close')">
        <span class="material-symbols-rounded">close</span>
      </button>

      <div class="card-showcase">
        <div class="card-image">
          <img :src="card.imageUrl || card.images.large || card.images.small" :alt="card.name">
        </div>
        
        <div class="card-info">
          <div class="info-header">
            <div>
              <h2>{{ card.name }}</h2>
              <div class="set-info">
                <img 
                  v-if="card.set.images && card.set.images.symbol" 
                  :src="card.set.images.symbol" 
                  :alt="card.set.name"
                  class="set-symbol"
                >
                <span>{{ card.set.name }}</span>
              </div>
            </div>
            <img 
              v-if="card.set.images && card.set.images.logo" 
              :src="card.set.images.logo" 
              :alt="card.set.name"
              class="set-logo"
            >
          </div>

          <div class="details-grid">
            <div class="detail-item">
              <span class="label">Rarity</span>
              <span class="value">{{ card.rarity || 'Unknown' }}</span>
            </div>
            <div class="detail-item">
              <span class="label">Card Number</span>
              <span class="value">{{ card.number || 'N/A' }}/{{ card.set.total }}</span>
            </div>
            <div class="detail-item">
              <span class="label">Release Date</span>
              <span class="value">{{ formatDate(card.set.releaseDate) }}</span>
            </div>
            <div class="detail-item">
              <span class="label">Market Price</span>
              <span class="value price">{{ formatPrice(card.price) }}</span>
            </div>
          </div>

          <div class="card-description" v-if="card.flavorText">
            <p>{{ card.flavorText }}</p>
          </div>

          <div class="action-buttons" v-if="auth.state.session">
            <button 
              v-if="storeProduct"
              class="action-btn store"
              @click="goToStore"
            >
              <span class="material-symbols-rounded">shopping_cart</span>
              Buy in Store - €{{ storeProduct.price.toFixed(2) }}
            </button>
            <button 
              class="action-btn collection"
              :class="{ active: isInCollection }"
              @click="toggleCollection"
            >
              <span class="material-symbols-rounded">
                {{ isInCollection ? 'library_add_check' : 'library_add' }}
              </span>
              {{ isInCollection ? 'In Collection' : 'Add to Collection' }}
            </button>
            <button 
              class="action-btn wishlist"
              :class="{ active: isInWishlist }"
              @click="toggleWishlist"
            >
              <span class="material-symbols-rounded">
                {{ isInWishlist ? 'favorite' : 'favorite_border' }}
              </span>
              {{ isInWishlist ? 'In Wishlist' : 'Add to Wishlist' }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import auth from '@/store/auth'
import collectionsStore from '@/store/collections'
import { supabase } from '@/services/supabase'

export default {
  name: 'CardDetailsModal',
  props: {
    card: {
      type: Object,
      required: true
    }
  },
  setup(props) {
    const router = useRouter()
    const storeProduct = ref(null)

    const checkStoreAvailability = async () => {
      try {
        const { data } = await supabase
          .from('products')
          .select('*')
          .eq('card_id', props.card.id)
          .eq('active', true)
          .gt('stock', 0)
          .single()
        
        storeProduct.value = data
      } catch (error) {
        console.error('Error checking store availability:', error)
      }
    }

    const goToStore = () => {
      if (storeProduct.value) {
        router.push({
          name: 'Store',
          query: { product: storeProduct.value.id }
        })
      }
    }

    onMounted(() => {
      checkStoreAvailability()
    })

    const isInCollection = computed(() => collectionsStore.isInCollection(props.card.id))
    const isInWishlist = computed(() => collectionsStore.isInWishlist(props.card.id))

    const toggleCollection = () => {
      if (!auth.state.session) return
      if (isInCollection.value) {
        collectionsStore.removeFromCollection(props.card.id)
      } else {
        collectionsStore.addToCollection(props.card)
      }
    }

    const toggleWishlist = () => {
      if (!auth.state.session) return
      if (isInWishlist.value) {
        collectionsStore.removeFromWishlist(props.card.id)
      } else {
        collectionsStore.addToWishlist(props.card)
      }
    }

    return {
      auth,
      isInCollection,
      isInWishlist,
      toggleCollection,
      toggleWishlist,
      storeProduct,
      goToStore
    }
  },
  methods: {
    formatDate(dateStr) {
      if (!dateStr) return 'N/A'
      return new Date(dateStr).toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      })
    },
    formatPrice(price) {
      if (price === 'N/A') return 'N/A'
      return `€${Number(price).toFixed(2)}`
    }
  }
}
</script>

<style scoped>
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  display: grid;
  place-items: center;
  z-index: 1000;
  padding: 24px;
}

.modal-content {
  background: var(--card-background);
  border-radius: 24px;
  width: 100%;
  max-width: 900px;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  box-shadow: var(--shadow-xl);
  animation: modalEnter 0.3s cubic-bezier(0.2, 0.8, 0.2, 1);
}

.close-button {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: none;
  background: var(--surface-background);
  color: var(--text-secondary);
  cursor: pointer;
  display: grid;
  place-items: center;
  transition: all 0.2s ease;
  z-index: 1;
}

.close-button:hover {
  background: var(--background-elevated);
  color: var(--text-primary);
  transform: scale(1.1);
}

.card-showcase {
  display: grid;
  grid-template-columns: minmax(300px, 1fr) 1.5fr;
  gap: 32px;
  padding: 32px;
}

.card-image {
  position: sticky;
  top: 32px;
}

.card-image img {
  width: 100%;
  height: auto;
  border-radius: 16px;
  box-shadow: var(--shadow-lg);
}

.info-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 24px;
}

.info-header h2 {
  font-size: 28px;
  margin: 0 0 8px 0;
  letter-spacing: -0.02em;
}

.set-info {
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--text-secondary);
  font-size: 15px;
}

.set-symbol {
  width: 24px;
  height: 24px;
}

.set-logo {
  width: 120px;
  height: auto;
}

.details-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin: 24px 0;
}

.detail-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.label {
  font-size: 13px;
  color: var(--text-secondary);
}

.value {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
}

.value.price {
  color: var(--accent-primary);
}

.card-description {
  margin: 24px 0;
  padding: 16px;
  background: var(--surface-background);
  border-radius: 12px;
  color: var(--text-secondary);
  font-size: 14px;
  line-height: 1.6;
  font-style: italic;
}

.action-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 24px;
}

.action-btn {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 24px;
  border: none;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.action-btn.collection {
  background: var(--accent-secondary);
  color: var(--accent-primary);
}

.action-btn.collection:hover,
.action-btn.collection.active {
  background: var(--accent-primary);
  color: white;
}

.action-btn.wishlist {
  background: var(--background-elevated);
  color: var(--text-secondary);
}

.action-btn.wishlist:hover,
.action-btn.wishlist.active {
  background: var(--system-red);
  color: white;
}

.action-btn.store {
  background: var(--system-green);
  color: white;
  flex: 2; /* Make the store button wider */
}

.action-btn.store:hover {
  background: var(--system-green-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

@keyframes modalEnter {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@media (max-width: 768px) {
  .card-showcase {
    grid-template-columns: 1fr;
    gap: 24px;
  }

  .card-image {
    position: relative;
    top: 0;
    max-width: 300px;
    margin: 0 auto;
  }

  .info-header {
    flex-direction: column;
    gap: 16px;
  }

  .set-logo {
    width: 100px;
  }

  .action-buttons {
    flex-direction: column;
  }
  
  .action-btn {
    width: 100%;
  }
  
  .action-btn.store {
    order: -1; /* Show store button first on mobile */
  }
}
</style>
